import React, { useState } from 'react';
import { useTable, useFilters, useGlobalFilter, usePagination } from 'react-table';
import '../../css/user_manage.css'
import { FiSearch } from 'react-icons/fi';
import UserInfo from './user_info';
import Swal from 'sweetalert2'

function Table({ columns, data, status, setStatus }) {
  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    page,
    prepareRow,
    setGlobalFilter,
    canPreviousPage,
    canNextPage,
    pageOptions,
    pageCount,
    gotoPage,
    nextPage,
    previousPage,
    setPageSize,
    state: { pageIndex, pageSize }
  } = useTable(
    {
      columns,
      data,
      initialState: { pageIndex: 0, pageSize: 10 }
    },
    useFilters,
    useGlobalFilter,
    usePagination
  );

  // 필터
  const [filterInput, setFilterInput] = useState('');

  // 검색창 변경
  const handleFilterChange = e => {
    const value = e.target.value || undefined;
    setGlobalFilter(value);
    setFilterInput(value);
  };

  // 상태: 수정 중 여부
  const [isEditing, setIsEditing] = useState(false);

  // 선택한 행의 데이터 상태
  const [selectedRowData, setSelectedRowData] = useState(null);

  // 수정 버튼 클릭 핸들러
  const handleEditClick = (rowData) => {
    setIsEditing(true);
    setSelectedRowData(rowData);
  };

  // 삭제 버튼 클릭 핸들러
  const handleDeleteClick = (rowData) => {
    Swal.fire({
      title: '사용자를 삭제하시겠습니까?',
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: '삭제',
      cancelButtonText: '취소',
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
    }).then((res) => {
      if (res.isConfirmed) {
        fetch('http://ceprj.gachon.ac.kr:60006/api/admin/member/delete/' + rowData.login_id, {
          method: 'DELETE',
          headers: {
            'Content-Type': 'application/json',
          },
        })
          .then(response => {
            if (response.ok) {
              Swal.fire({
                title: "삭제 되었습니다!",
                icon: "success"
              });
              console.log('성공했어');
              setStatus(1);
            } else {
              throw new Error('유저 정보 수정에 실패했습니다.');
            }
          })
          .catch(error => {
            console.error('Error:', error);
          });
      }
    });
  };

  const handleIsEditingChange = () => {
    setIsEditing(false);
  };

  return (
    <>
      {isEditing ? (
        <UserInfo rowData={selectedRowData} handleIsEditingChange={handleIsEditingChange} setStatus={setStatus} status={status} />
      ) : (
        <>
          <div className='tableBoxOut'>
            <div className='search-conatiner'>
              <input
                className='search'
                value={filterInput}
                onChange={handleFilterChange}
                placeholder="검색어를 입력해주세요." />
              <FiSearch className='search-icon' />
            </div>
            <table className='usertable' {...getTableProps()}>
              <thead>
                {headerGroups.map(headerGroup => (
                  <tr className='columns' {...headerGroup.getHeaderGroupProps()}>
                    {headerGroup.headers.map(column => (
                      <th className='userth' {...column.getHeaderProps()}>{column.render('Header')}</th>
                    ))}
                    <th className='userth'></th>
                  </tr>
                ))}
              </thead>
              <tbody {...getTableBodyProps()}>
                {page.map(row => {
                  prepareRow(row);
                  return (
                    <tr className='usertr' {...row.getRowProps()}>
                      {row.cells.map(cell => {
                        return <td className='usertd' {...cell.getCellProps()}>{cell.render('Cell')}</td>;
                      })}
                      <td className='usertd'>
                        <button className='bt_u' onClick={() => handleEditClick(row.original)}>수정</button>
                        <button className='bt_u' onClick={() => handleDeleteClick(row.original)}>삭제</button>
                      </td>
                    </tr>
                  );
                })}
              </tbody>
            </table>
            <div className="pagination">
              <button className='page_button' onClick={() => gotoPage(0)} disabled={!canPreviousPage}>
                {'<<'}
              </button>
              <button className='page_button' onClick={() => previousPage()} disabled={!canPreviousPage}>
                {'<'}
              </button>
              {pageOptions.map((pageOption, i) => (
                <button 
                  key={i}
                  onClick={() => gotoPage(pageOption)}
                  disabled={pageOption === pageIndex}
                  className={pageIndex === pageOption ? 'num_button' : 'num_button2'}
                >
                  {pageOption + 1}
                </button>
              ))}
              <button className='page_button' onClick={() => nextPage()} disabled={!canNextPage}>
                {'>'}
              </button>
              <button className='page_button' onClick={() => gotoPage(pageCount - 1)} disabled={!canNextPage}>
                {'>>'}
              </button>
              
             
              <select className='Select'
                value={pageSize}
                onChange={e => {
                  setPageSize(Number(e.target.value));
                }}
              >
                {[10, 20, 30, 40, 50].map(pageSize => (
                  <option key={pageSize} value={pageSize}>
                    Show {pageSize}
                  </option>
                ))}
              </select>
            </div>
          </div>
        </>
      )}
    </>
  );
}

export default Table;
