import React from "react";

const sizes = {
  xs: "text-[10px] font-normal",
  lg: "text-[15px] font-normal",
  s: "text-[11px] font-normal",
  "2xl": "text-[17px] font-normal",
  "3xl": "text-xl font-normal",
  xl: "text-base font-normal",
  md: "text-xs font-normal",
};

const Text = ({ children, className = "", as, size = "lg", ...restProps }) => {
  const Component = as || "p";

  return (
    <Component className={`text-white-A700 font-roboto ${className} ${sizes[size]}`} {...restProps}>
      {children}
    </Component>
  );
};

export { Text };
