import React, { useState } from 'react';
import { useTable, useFilters, useGlobalFilter, usePagination } from 'react-table';
import '../../css/ingredient.css';
import { FiSearch } from 'react-icons/fi';
import IngredientInfo from 'sidebar/page/ingredient/ingredient_info';

// 테이블 컴포넌트
function IngredientTable({ columns, data, status, setStatus }) {
  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    rows,
    prepareRow,
    setFilter,
    page, // 페이지로 나뉜 데이터
    canPreviousPage,
    canNextPage,
    pageOptions,
    pageCount,
    gotoPage,
    nextPage,
    previousPage,
    setPageSize,
    state: { pageIndex, pageSize },
  } = useTable(
    {
      columns,
      data,
      initialState: { pageIndex: 0, pageSize: 10 }
    },
    useFilters,
    useGlobalFilter,
    usePagination
  );

  // 필터
  const [filterInput, setFilterInput] = useState('');
  const [label, setLabel] = useState('');

  // 검색창 변경
  const handleFilterChange = (e) => {
    const value = e.target.value || undefined;
    setFilter('name', value); // 여기서 'yourColumnId'를 실제 컬럼 ID로 변경해주세요.
    setFilterInput(value);
  };

  const initialData = {
    allergy_status: false,
    danger_status: false,
    features: [],
    grade: 1,
    id: 5,
    name: "",
    purposes: [{}],
    skinTypeFeatures: [{ skin_type: 'DRY', skinDescription: '', positivity_status: false }]
  };

  const [isEditing, setIsEditing] = useState(false);
  const [selectedRowData, setSelectedRowData] = useState(null);
  const [pageRange, setPageRange] = useState([0, 9]); // 페이지 범위를 관리하는 상태

  const handleEditClick = (rowData) => {
    setIsEditing(true);
    setLabel('수정');
    setSelectedRowData(rowData);
  };

  const handleCreateClick = () => {
    setIsEditing(true);
    setLabel('생성');
    setSelectedRowData(initialData);
  };

  const goBack = () => {
    setIsEditing(false);
  };

  const handleNextPageRange = () => {
    setPageRange(([start, end]) => [start + 10, end + 10]);
  };

  const handlePreviousPageRange = () => {
    setPageRange(([start, end]) => [start - 10, end - 10]);
  };

  const currentPageOptions = pageOptions.slice(pageRange[0], pageRange[1] + 1);

  return (
    <>
      {isEditing ? (
        <IngredientInfo label={label} goBack={goBack} rowData={selectedRowData} setStatus={setStatus} status={status} />
      ) : (
        <div className='tableBoxOut'>
          <div>
            <div className='bt_box'>
              <button className='bt' onClick={handleCreateClick}>성분 추가하기</button>
            </div>
            <div className='search-conatinerByI'>
              <input
                className='search'
                value={filterInput}
                onChange={handleFilterChange}
                placeholder="검색어를 입력해주세요." />
              <FiSearch className='search-icon' />
            </div>
            <table className='ingredienttable' {...getTableProps()}>
              <thead>
                {headerGroups.map(headerGroup => (
                  <tr className='columns' {...headerGroup.getHeaderGroupProps()}>
                    {headerGroup.headers.map(column => (
                      <th className='ingredientth' {...column.getHeaderProps()}>{column.render('Header')}</th>
                    ))}
                    <th className='ingredientth'></th>
                  </tr>
                ))}
              </thead>
              <tbody {...getTableBodyProps()}>
                {page.map(row => {
                  prepareRow(row);
                  return (
                    <tr {...row.getRowProps()}>
                      {row.cells.map(cell => {
                        const content = cell.value === true ? <h2>O</h2> : cell.value === false ? <h2>X</h2> : cell.render('Cell');
                        return <td className='ingredienttd' {...cell.getCellProps()}>{content}</td>;
                      })}
                      <td className='ingredienttd'>
                        <button className='bt' onClick={() => handleEditClick(row.original)}>관리</button>
                      </td>
                    </tr>
                  );
                })}
              </tbody>
            </table>
            <div className="pagination">
              <button className='page_button' onClick={() => gotoPage(0)} disabled={!canPreviousPage}>
                {'<<'}
              </button>
              {pageRange[0] > 0 && (
                <button className='page_button' onClick={handlePreviousPageRange}>
                  {'<'}
                </button>
              )}
              {currentPageOptions.map((pageOption, i) => (
                <button
                  key={i}
                  onClick={() => gotoPage(pageOption)}
                  disabled={pageOption === pageIndex}
                  className={pageIndex === pageOption ? 'num_button' : 'num_button2'}
                >
                  {pageOption + 1}
                </button>
              ))}
              {pageRange[1] < pageOptions.length - 1 && (
                <button className='page_button' onClick={handleNextPageRange}>
                  {'>'}
                </button>
              )}
              
              <button className='page_button' onClick={() => gotoPage(pageCount - 1)} disabled={!canNextPage}>
                {'>>'}
              </button>
              <select className='Select'
                value={pageSize}
                onChange={e => {
                  setPageSize(Number(e.target.value));
                }}
              >
                {[10, 20, 30, 40, 50].map(pageSize => (
                  <option key={pageSize} value={pageSize}>
                    Show {pageSize}
                  </option>
                ))}
              </select>
            </div>
          </div>
        </div>
      )}
    </>
  );
}

export default IngredientTable;
