import React, { useState } from "react";
import LinkRow from "../../component/cosmetic_link/link_row";
import Swal from "sweetalert2";

const CosmeticInfo = ({ rowData, goBack }) => {
    const [data, setData] = useState(rowData);
    const [purchaseLinks, setPurchaseLinks] = useState(data.cosmeticPurchaseLinks);

    const handleNameChange = (e) => {
        console.log(data);
        setData(prevData => ({
            ...prevData,
            cosmeticPurchaseLinks: e.target.value
        }));

    };

    // 링크 변경 핸들러
    const handleLinkChange = (index, fieldName, newValue) => {
        const newPurchaseLinks = [...purchaseLinks];
        newPurchaseLinks[index][fieldName] = newValue;
        setPurchaseLinks(newPurchaseLinks);
    };

    const handleRevise = () => {
        Swal.fire({
            title: '데이터를 변경하시겠습니까?',
            icon: 'warning',
            showCancelButton: true,
            confirmButtonText: '확인',
            cancelButtonText: '취소',
            confirmButtonColor: "#3085d6",
            cancelButtonColor: "#d33",
        }).then((res) => {
            if (res.isConfirmed) {
                fetch('http://ceprj.gachon.ac.kr:60006/api/admin/link/update/' + data.id, {
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/json',
                    },
                    body: JSON.stringify(purchaseLinks),
                })
                    .then(response => {
                        if (response.ok) {
                            Swal.fire({
                                title: "변경 되었습니다!",
                                icon: "success"
                              });
                              goBack();
                        } else {
                            Swal.fire({
                                title: "변경 실패 하였습니다!",
                                icon: "success"
                              });
                              goBack();
                            throw new Error('링크 데이터 수정에 실패했습니다.');
                        }
                    })
                    .catch(error => {
                        console.error('Error:', error);
                    });
            }
        });
    }

    const handleDelete = () => {
        Swal.fire({
            title: '해당 데이터를 삭제하시겠습니까?',
            icon: 'warning',
            showCancelButton: true,
            confirmButtonText: '삭제',
            cancelButtonText: '취소',
            confirmButtonColor: "#3085d6",
            cancelButtonColor: "#d33",
            }).then((res) => {
              if (res.isConfirmed) {
                fetch('http://ceprj.gachon.ac.kr:60006/api/admin/cosmetic/delete/' + data.id, {
            method: 'DELETE',
            headers: {
              'Content-Type': 'application/json',
            },
          })
            .then(response => {
              if (response.ok) {
                Swal.fire({
                  title: "삭제 되었습니다!",
                  icon: "success"
                });
                goBack();
                console.log('성공했어');
              } else {
                throw new Error('삭제에 실패했습니다.');
              }
            })
            .catch(error => {
              console.error('Error:', error);
            });
                  }
            });
    }


    return (
        <div className="LLLL">
        <div className="total-link-container">
         
            <div className="link-container">
                <img className='infoImage' src={data.image_path} alt="이미지" />
                <div className="Link_name">{data.name}</div>
                <div className="Link_Date">최근 업데이트 날짜 : {data.latest_update_date}</div>
            </div>
            <div className="Link_Input_Box">
                
                <div >
                    {data.cosmeticPurchaseLinks.map((purchaseLink, index) => (
                        <div className="purchase-container" key={index}>
                            <input
                                className="purchaseSite"
                                type="text"
                                value={purchaseLink.purchaseSite}/>
                            <div>
                                <LinkRow label="가격" value={purchaseLink.price} onChange={(newValue) => handleLinkChange(index, 'price', newValue)}/>
                                <LinkRow label="링크" value={purchaseLink.url} onChange={(newValue) => handleLinkChange(index, 'url', newValue)} />
                            </div>
                        </div>
                    ))}
                </div>
                <div className="link_bts_box">
                    <button onClick={handleRevise} className='bt' >수정</button>
                    <button onClick={handleDelete} className='bt' >삭제</button>
                    <button onClick={goBack} className='bt' >취소</button>
                </div>
            </div>
            </div>
        </div>
    );
};

export default CosmeticInfo;