import React, {useState, useEffect} from "react"
import IngredientTable from "../../component/ingredient/ingredient_table"



  
  const columns = [
    { Header: '성분 이름', accessor: 'name' },
    { Header: '성분 등급', accessor: 'grade' },
    { Header: '위험 여부', accessor: 'danger_status' },
    { Header: '알레르기 유발 여부', accessor: 'allergy_status' },
  ];

const Ingredient = () => {
  const [status, setStatus] = useState(0);
    const [data, setData] = useState([]);

    useEffect(() => {
      console.log("SSS = " + status );
      const fetchData = async () => {
        try {
          // 서버로부터 데이터를 가져오는 API 호출
          const response = await fetch('http://ceprj.gachon.ac.kr:60006/api/amin/ingredient');
          const fetchedData = await response.json();
          setData(fetchedData);
        } catch (error) {
          console.error('Error fetching data:', error);
        }
        setStatus(0);
      };
    
      fetchData();
    }, [status]);


    return  (
    <div>
      <div className='titleBox'>
      <div className="title">성분 관리</div>
      <div className="title2">Ingredient Manage for admin</div><hr className="n1"/><br /><br /><br />
      </div>
    
        <div><IngredientTable data={data} columns={columns} status={status} setStatus={setStatus} /></div>

    </div>
    )
}

export default Ingredient