import React, { useState, useEffect } from 'react';
import Table from '../../component/user_manage/table';
import '../../css/user_manage.css';

const ManageUser = () => {
  const [status, setStatus] = useState(0);
  const [data, setData] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      try {
        // 서버로부터 데이터를 가져오는 API 호출
        const response = await fetch('http://ceprj.gachon.ac.kr:60006/api/admin/member/info');
        const fetchedData = await response.json();
        setData(fetchedData);
      } catch (error) {
        console.error('Error fetching data:', error);
      }
      setStatus(0);
    };

    fetchData();
  }, [status]);

  const columns = [
    { Header: '로그인ID', accessor: 'login_id' },
    { Header: '이름', accessor: 'name' },
    { Header: '생년 월일', accessor: 'birth' },
    { Header: '이메일', accessor: 'email' },
    { Header: '성별', accessor: 'gender' },
  ];

  return (
    <div>
      <div className='titleBox'>
      <div className="title">사용자 관리</div>
      <div className="title2">User Manage for admin</div><hr className="n1"/><br /><br /><br />
      </div>
      
      <Table columns={columns} data={data} status={status} setStatus={setStatus} />
      
    </div>
  );
};

export default ManageUser;
