import React, {useState, useEffect} from "react"
import LinkTable from "../../component/cosmetic_link/link_table"

const CosmeticLink = () => {
  const [status, setStatus] = useState(0);
  const [data, setData] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch('http://ceprj.gachon.ac.kr:60006/api/admin/link');
        const fetchedData = await response.json();
        setData(fetchedData);
      } catch (error) {
        console.error('Error fetching data:', error);
      }
      setStatus(0);
    };

    fetchData();
  }, [status]);

  const columns = [
    { Header: '', accessor: 'image_path' },
    { Header: '날짜', accessor: 'latest_update_date' },
    { Header: '이름', accessor: 'name' },
  ];

    return  (
    <div>
        <div className='titleBox'>
      <div className="title">화장품 구매처 관리</div>
      <div className="title2">Cosmetics Purchase Manage for admin</div><hr className="n1"/><br /><br /><br />
      </div>
        <div><LinkTable data={data} columns={columns} status={status} setStatus={setStatus} /></div>
    </div>
    )
}

export default CosmeticLink