import React, { useState } from "react";
import '../../css/AI_manage.css';
import { useTable, useFilters, useGlobalFilter } from 'react-table';
import { FiSearch } from "react-icons/fi";
import Swal from "sweetalert2";

const AIVersion = ({update, setUpdate, model, onClick, data, columns, version }) => {
    const {
        getTableProps,
        getTableBodyProps,
        headerGroups,
        rows,
        prepareRow,
        setGlobalFilter,
    } = useTable(
        {
            columns,
            data,
        },
        useFilters,
        useGlobalFilter
    );

    const [selectedVersion, setSelectedVersion] = useState(version);
    const [selectedRowIndex, setSelectedRowIndex] = useState(null);
    const [filterInput, setFilterInput] = useState('');

    // 검색창 변경
    const handleFilterChange = e => {
        const value = e.target.value || undefined;
        setGlobalFilter(value);
        setFilterInput(value);
    };

    const handleRowClick = (row, index) => {
        setSelectedVersion(row.original.version);
        setSelectedRowIndex(index);
    };

    const change = async () => {
        console.log(model);
        if (selectedRowIndex === null || selectedRowIndex === undefined) {
            Swal.fire({
                title: "이미 선택된 모델입니다.",
                icon: "error"
            });
            return;
        }
        const selectedRowData = rows[selectedRowIndex].original;
        const latestValue = selectedRowData.latest;

        
        if (latestValue == true) {
            Swal.fire({
                title: "이미 선택된 모델입니다.",
                icon: "error"
            });
            return;
        }
        console.log(JSON.stringify(selectedRowData));
        if(model == 'OCR'){
        if (selectedRowIndex !== null) {
            try {
                Swal.fire({
                    title: '모델을 변경 하시겠습니까',
                    icon: 'warning',
                    showCancelButton: true,
                    confirmButtonText: '확인',
                    cancelButtonText: '취소',
                    confirmButtonColor: "#3085d6",
                    cancelButtonColor: "#d33",
                    }).then((res) => {
                      if (res.isConfirmed) {
                        const response = fetch('http://ceprj.gachon.ac.kr:60006/api/admin/AI/latest/change/OCR', {
                            method: 'POST',
                            headers: {
                                'Content-Type': 'application/json',
                            },
                            body: JSON.stringify(selectedRowData),
                    })
                    .then(response => {
    
                    if (response.ok) {
                        Swal.fire({
                            title: "모델이 변경 되었습니다!",
                            icon: "success"
                        });
                        onClick();
                        setUpdate(1);
                        console.log('성공했어');
                    } else {
                        Swal.fire({
                            title: "모델이 변경 실패하였습니다.",
                            icon: "error"
                        });
                        throw new Error('유저 정보 수정에 실패했습니다.');
                      }
                })
            } 
            })
        }
        catch (error) {
            console.error('Error:', error);
    }
}
 
    }
    else{
        if (selectedRowIndex !== null) {
            try {
                Swal.fire({
                    title: '모델을 변경 하시겠습니까',
                    icon: 'warning',
                    showCancelButton: true,
                    confirmButtonText: '확인',
                    cancelButtonText: '취소',
                    confirmButtonColor: "#3085d6",
                    cancelButtonColor: "#d33",
                    }).then((res) => {
                      if (res.isConfirmed) {
                        const response = fetch('http://ceprj.gachon.ac.kr:60006/api/admin/AI/latest/change/Reco', {
                            method: 'POST',
                            headers: {
                                'Content-Type': 'application/json',
                            },
                            body: JSON.stringify(selectedRowData),
                    })
                    .then(response => {
    
                    if (response.ok) {
                        Swal.fire({
                            title: "모델이 변경 되었습니다!",
                            icon: "success"
                        });
                        onClick();
                        setUpdate(1);
                        console.log('성공했어');
                    } else {
                        Swal.fire({
                            title: "모델이 변경 실패하였습니다.",
                            icon: "error"
                        });
                        throw new Error('유저 정보 수정에 실패했습니다.');
                      }
                })
            } 
            })
        }
        catch (error) {
            console.error('Error:', error);
    }
}
    }
}
    

    return (
        <div style={{ marginTop: '100px' }} className='tableBoxOut'>
            <h2 className="versionSelectText">모델 버전 선택</h2>
            <div className='search-conatiner'>
                <input
                    className='search'
                    value={filterInput}
                    onChange={handleFilterChange}
                    placeholder="검색어를 입력해주세요." />
                <FiSearch className='search-icon' />
            </div>
            <table className='AItable' {...getTableProps()}>
                <thead>
                    {headerGroups.map(headerGroup => (
                        <tr className='columns' {...headerGroup.getHeaderGroupProps()}>
                            {headerGroup.headers.map(column => (
                                <th className='AIth' {...column.getHeaderProps()}>{column.render('Header')}</th>
                            ))}
                            <th className='AIth'></th>
                        </tr>
                    ))}
                </thead>
                <tbody {...getTableBodyProps()}>
                    {rows.map((row, index) => {
                        const isTargetRow = row.original.version === selectedVersion; // 클릭된 행인지 여부 확인
                        const rowStyles = {
                            backgroundColor: isTargetRow ? '#FEE1E1' : 'inherit', // 클릭된 행에 배경색 지정
                        };

                        prepareRow(row);
                        return (
                            <tr
                                className='AItr'
                                {...row.getRowProps()}
                                style={rowStyles}
                                onClick={() => handleRowClick(row, index)} // 클릭 이벤트 핸들러 연결
                            >
                                {row.cells.map(cell => {
                                    return <td className='AItd' {...cell.getCellProps()}>{cell.render('Cell')}</td>;
                                })}
                                <td className='AItd'></td>
                            </tr>
                        );
                    })}
                </tbody>
            </table>
            <td className='versionBts'>
                <button className='AIbt' onClick={change}>변경</button>
                <button className='AIbt' onClick={onClick}>취소</button>
            </td>
        </div>
    );
};

export default AIVersion;
