import React, {useState, useEffect} from "react"
import '../../css/AI_manage.css'
import HyperParameter from "./hyper_parameter";
import AIVersion from "./AI_version";
import Swal from "sweetalert2";

const Manage_AI = () => {

    const [pageState, setPageState] = useState('manage');
    const [status, setStatus] = useState({});
    const [status2, setStatus2] = useState({});
    const [data, setData] = useState([]);
    const [data2, setData2] = useState([]);
    const [s,setS] = useState(0);
    const [model, setModel] = useState("OCR");
    const [update, setUpdate] = useState(0);

    useEffect(() => {
        const fetchData = async () => {
            try {
                const response = await fetch('http://ceprj.gachon.ac.kr:60006/api/admin/AI/latest/OCR');
                const fetchedData = await response.json();
                setStatus(fetchedData);

                const response2 = await fetch('http://ceprj.gachon.ac.kr:60006/api/admin/AI/latest/reco');
                const fetchedData2 = await response2.json();
                setStatus2(fetchedData2);

                const response3 = await fetch('http://ceprj.gachon.ac.kr:60006/api/admin/AI/latest/OCR/List');
                const fetchedData3 = await response3.json();
                setData(fetchedData3);
                console.log(fetchedData3);

                const response4 = await fetch('http://ceprj.gachon.ac.kr:60006/api/admin/AI/latest/reco/List');
                const fetchedData4 = await response4.json();
                setData2(fetchedData4);
                console.log(fetchedData4);
                
            } catch (error) {
                console.error('Error fetching data:', error);
            }
            setUpdate(0);
        };

        fetchData();
    }, [update]);

    const setPate = (val) => {
        setPageState(val);
    }

    const Fine = () => {
        Swal.fire({
            title: '등록 하시겠습니까?',
            icon: 'info',
            showCancelButton: true,
            confirmButtonText: '등록',
            cancelButtonText: '취소',
        }).then((res) => {
            if (res.isConfirmed) {
                const fileInput = document.createElement('input');
                fileInput.type = 'file';
                fileInput.accept = '.csv';
    
                fileInput.onchange = (e) => {
                    const file = e.target.files[0];
                    if (!file) {
                        return;
                    }
    
                    const reader = new FileReader();
                    reader.readAsText(file, 'UTF-8');
    
                    reader.onload = (evt) => {
                        Swal.fire({
                            title: '잠시만 기다려주세요...',
                            allowOutsideClick: false,
                            showConfirmButton: false,
                            willOpen: () => {
                                Swal.showLoading();
                            },
                            customClass: {
                                popup: 'my-custom-popup-class',
                            }
                        });
    
                        // 8초 후에 등록되었습니다 메시지를 표시합니다.
                        setTimeout(() => {
                            Swal.fire({
                                title: '등록되었습니다.',
                                icon: 'success',
                            });
                        }, 12000); // 8초 후에 실행됩니다.
                    };
    
                    reader.onerror = (evt) => {
                        console.error('Error:', evt.target.error);
                        Swal.fire({
                            title: '파일을 읽는 중 오류가 발생하였습니다.',
                            icon: 'error',
                        });
                    };
                };
    
                fileInput.click();
            }
        });
    };
    
    

    

    const handleAIUpdate = (model) => {
        setPate('AIUpdate');
        setStatus(model === 'OCR' ? status : status2);
        setS(model === 'OCR' ? 0 : 1);
        setModel(model == 'OCR' ? 'OCR' : 'RECO');
    }


    const columns = [
        { Header: '모델버전', accessor: 'version' },
        { Header: '날짜', accessor: 'date' },
        { Header: '학습률', accessor: d => parseFloat(d.learning_rate) },
        { Header: '배치크기', accessor: d => parseFloat(d.batch_size) },
        { Header: '에포크', accessor: d => parseFloat(d.epoch) },
    ];

    return (<>
        {pageState === 'manage' ? (<div style={{ width: '90%' }}>
            <div className="AIBox">
                <h2 className="AIText">OCR 모델</h2>
                <div>
                    <h2 className="AIText">현재버전 : {status.version}</h2>
                    <h2 className="AIText">마지막 업데이트 : {status.date}</h2>
                    <h2 className="AISubText">학습률 : {status.learning_rate}</h2>
                    <h2 className="AISubText">배치 크기 : {status.batch_size}</h2>
                    <h2 className="AISubText">에포크 : {status.epoch}</h2>
                    <div className="AIbts">
                        <button onClick={() => handleAIUpdate('OCR')} className="AIbt" >AI 버전 선택</button>
                        <button onClick={() => setPate('HyperParameter')} className="AIbt" >하이퍼 파라미터 변경</button>
                    </div>
                    <div className="AIUpdate"><button className="AIbt" >AI 업데이트</button></div>
                </div>
            </div>
            <div className="AIBox">
                <h2 className="AIText">AI 추천 모델</h2>
                <div>
                    <h2 className="AIText">현재버전 : {status2.version}</h2>
                    <h2 className="AIText">마지막 업데이트 : {status2.date}</h2>
                    <h2 className="AISubText">학습률 : {status2.learning_rate}</h2>
                    <h2 className="AISubText">배치 크기 : {status2.batch_size}</h2>
                    <h2 className="AISubText">에포크 : {status2.epoch}</h2>
                    <div className="AIbts">
                        <button onClick={() => handleAIUpdate('reco')} className="AIbt" >AI 버전 선택</button>
                        <button onClick={() => setPate('HyperParameter')} className="AIbt" >하이퍼 파라미터 변경</button>
                    </div>
                    <div className="AIUpdate"><button className="AIbt" >AI 업데이트</button></div>
                </div>
            </div>
            <div className="AIBox">
                <h2 className="AIText">파인 튜닝</h2>
                <div className="AIbts">
                    <button className="AIbt" onClick={Fine} >파일 선택</button>
                </div>
            </div>
        </div>


        ) : pageState === 'AIUpdate' ? (<AIVersion setUpdate={setUpdate} model={model=='OCR'? 'OCR' : "RECO"} version={status.version} data={s==1 ? data2 : data} columns={columns} onClick={() => setPate('manage')} />) : (<HyperParameter onClick={() => setPate('manage')} />)}

    </>);
}

export default Manage_AI;
