import React, { useState } from "react";
import IngredientInput from "sidebar/component/ingredient/ingredient_input";
import IngredientRadio from "sidebar/component/ingredient/ingredient_radio";
import '../../css/ingredient.css'
import IngredientInput2 from "sidebar/component/ingredient/ingredient_input2";
import Swal from "sweetalert2";

const IngredientInfo = ({ label, rowData, goBack, status, setStatus }) => {
    
    const [data, setData] = useState(rowData);
    const [skinTypesFeaturesData, setSkinTypesFeaturesData] = useState(data.skinTypeFeatures);
    
    // 링크 변경
    const handleSkinEffectChange = (index, fieldName, newValue) => {
        const newskinTypeEffect = [...skinTypesFeaturesData];
        newskinTypeEffect[index][fieldName] = newValue;
        setSkinTypesFeaturesData(newskinTypeEffect);
    };

    const handleSkinEffectsChange = (index, fieldName, newValue) => {
        const newSkinTypesData = [...skinTypesFeaturesData];
        newSkinTypesData[index][fieldName] = TFTextChangeR(newValue);
        setSkinTypesFeaturesData(newSkinTypesData);
    };

    const handleSkinTypesChange = (index, fieldName, newValue) => {
        const newskinTypeEffect = [...skinTypesFeaturesData];
        newskinTypeEffect[index][fieldName] = skinTypeCTextChangeR(newValue);
        setSkinTypesFeaturesData(newskinTypeEffect);
    };

    // 이름 변경
    const handleNameChange = (val) => {
        setData(prevData => ({
            ...prevData,
            name: val
        }));
    };

    // 위험 변경
    const handleDangerChange = (val) => {
        setData(prevData => ({
            ...prevData,
            danger_status: dangerTextChangeR(val)
        }));
    };
    
    // 목적 변경
    const handlePurposeChange = (value) => {
        const newPurposes = value.split(',').map((purpose, index) => {
            const existingPurpose = data.purposes[index];
            const newPurpose = {
                purpose: purpose.trim()
            };
            if (existingPurpose && existingPurpose.id !== undefined) {
                newPurpose.id = existingPurpose.id;
            } else {
                delete newPurpose.id; // id가 존재하지 않는 경우 해당 키를 삭제
            }
            return newPurpose;
        });
        setData(prevData => ({
            ...prevData,
            purposes: newPurposes
        }));
    };

    


    // 기능 변경
    const handleFeaturesChange = (value) => {
        const newFeatures = value.split(',').map((feature, index) => {
            const existingFeature = data.features[index];
            const newFeature = {
                feature: feature.trim()
            };
            if (existingFeature && existingFeature.id !== undefined) {
                newFeature.id = existingFeature.id;
            }
            return newFeature;
        });
        setData(prevData => ({
            ...prevData,
            features: newFeatures
        }));
    };
    
    

    // 알레르기 위험 변경
    const handleADangerChange = (val) => {
        setData(prevData => ({
            ...prevData,
            allergy_status: dangerTextChangeR(val)
        }));
    };

    // 성별 변경
    const handleGradeChange = (val) => {
        setData(prevData => ({
            ...prevData,
            grade: val
        }));
    };
    
    const dangerData = [
        { name: "O" },
        { name: "X" },
    ];

    const gradeData = [
        { name: "1" },
        { name: "2" },
        { name: "3" },
        { name: "4" },
        { name: "5" },
        { name: "6" },
        { name: "7" },
        { name: "8" },
        { name: "9" },
    ];

    const skinTypeData = [
        { name: "건성" },
        { name: "지성" },
        { name: "민감성" },
    ];

    const TFData = [
        { name: "긍정" },
        { name: "부정" },
    ];

    const skinTypeCTextChange = (val) => {
        if(val === 'DRY'){
            return '건성'
        }else if(val === 'OILY'){
            return '지성'
        }else{
            return '민감성'
        }
    }

    const skinTypeCTextChangeR = (val) => {
        if(val === '건성'){
            return 'DRY'
        }else if(val === '지성'){
            return 'OILY'
        }else{
            return 'SENSITIVE'
        }
    }

    const TFTextChange = (val) => {
        if(val){
            return '긍정'
        }else{
            return '부정'
        }
    }

    const TFTextChangeR = (val) => {
        if(val == '긍정'){
            return true
        }else{
            return false
        }
    }

    const dangerTextChange = (val) => {
        if(val){
            return 'O'
        }else{
            return 'X'
        }
    }

    const dangerTextChangeR = (val) => {
        if(val == 'O'){
            return true
        }else{
            return false
        }
    }

    const handleRevise = () => {

        

        

        const url = label == '수정' ? `http://ceprj.gachon.ac.kr:60006/api/admin/ingredient/update/${data.id}` : 'http://ceprj.gachon.ac.kr:60006/api/admin/ingredient/add'
        if(label == '생성'){
            delete data.id;
        }
        console.log(data);
        if(label == '생성'){
            Swal.fire({
                title: '성분을 등록 하시겠습니까?',
                icon: 'warning',
                showCancelButton: true,
                confirmButtonText: '확인',
                cancelButtonText: '취소',
                confirmButtonColor: "#3085d6",
                cancelButtonColor: "#d33",
            }).then((res) => {
                if (res.isConfirmed) {
                    fetch(url, {
                        method: 'POST',
                        headers: {
                            'Content-Type': 'application/json',
                        },
                        body: JSON.stringify(data),
                    })
                        .then(response => {
                            Swal.fire({
                                title: "성분이 등록 되었습니다.",
                                icon: "success"
                              });
                            
                                console.log('성공');
                                console.log(data);
                                goBack();
                                setStatus(1);
                            
                        })
                        .catch(error => {
                            console.error('Error:', error);
                        });
                }
            });

        }
        else{

            Swal.fire({
                title: '성분를 변경하시겠습니까?',
                icon: 'warning',
                showCancelButton: true,
                confirmButtonText: '확인',
                cancelButtonText: '취소',
                confirmButtonColor: "#3085d6",
                cancelButtonColor: "#d33",
            }).then((res) => {
                if (res.isConfirmed) {
                    fetch(url, {
                        method: 'POST',
                        headers: {
                            'Content-Type': 'application/json',
                        },
                        body: JSON.stringify(data),
                    })
                        .then(response => {
                           
                                Swal.fire({
                                    title: "성분이 변경 되었습니다.",
                                    icon: "success"
                                  });
                                console.log('성공');
                                console.log(data);
                                goBack();
                                setStatus(1);
                            
                        })
                        .catch(error => {
                            console.error('Error:', error);
                        });
                }
            });

        }
        
    }

    const handleDelete = () => {



        Swal.fire({
            title: '해당 데이터를 삭제하시겠습니까?',
            icon: 'warning',
            showCancelButton: true,
            confirmButtonText: '삭제',
            cancelButtonText: '취소',
            confirmButtonColor: "#3085d6",
            cancelButtonColor: "#d33",
            }).then((res) => {
              if (res.isConfirmed) {
                fetch('http://ceprj.gachon.ac.kr:60006/api/admin/ingredient/delete/' + data.id, {
            method: 'DELETE',
            headers: {
              'Content-Type': 'application/json',
            },
          })
            .then(response => {
              if (response.ok) {
                Swal.fire({
                  title: "삭제 되었습니다!",
                  icon: "success"
                });
                goBack();
                setStatus(1);
                console.log('성공했어');
              } else {
                Swal.fire({
                    title: "삭제 실패",
                    icon: "error"
                  });
                throw new Error('삭제에 실패했습니다.');
              }
            })
            .catch(error => {
                Swal.fire({
                    title: "삭제 실패",
                    icon: "error"
                  });
              console.error('Error:', error);
            });
                  }
            });
    }

    const handleAddSkinTypeFeatures = () => {
        setSkinTypesFeaturesData([...skinTypesFeaturesData, { skin_type: 'DRY', skinDescription: '', positivity_status: false }])
        setData(prevData => ({
            ...prevData,
            skinTypeFeatures: skinTypesFeaturesData
        }));
    };
    // skinTypesData skinTypesEffectData skinTypeEffectData
    const handleRemoveSkinTypeFeatures = () => {
        const skinTypeFeatureData = [...skinTypesFeaturesData];
        skinTypeFeatureData.splice(skinTypesFeaturesData.length-1, 1); 
        console.log(skinTypeFeatureData);
        setData(prevData => ({
            ...prevData,
            skinTypeFeatures: skinTypeFeatureData
        }));
    };

    return (
    <div className="ingredient_Total_Box">
        <div className='infotext'>성분 {label}</div>
        <div className="ingredientInfo-container">
            <div className="ingredientText-container">
                <IngredientInput label='성분 이름' onChange={handleNameChange} value={data.name}/>
                <IngredientInput label='성분 목적' onChange={handlePurposeChange} value={data.purposes.map(item => item.purpose).join(', ')}/>
                <IngredientInput label='성분 기능' onChange={handleFeaturesChange} value={data.features.map(item => item.feature).join(', ')}/>
                <div>
                    <span style={{fontSize:"16px", fontWeight:"600"}}>피부 타입별 효과</span>
                    {/* <div className="ingredient_radio_Box"> */}
                    <div >
                    {data.skinTypeFeatures.map((item, index) => (
                        <div style={{marginBottom:"50px", borderBottom:"1px solid grey"}}>
                            <IngredientRadio text='피부 타입' data={skinTypeData} selectedValue={skinTypeCTextChange(item.skin_type)} handleChange={(newValue) => handleSkinTypesChange(index, 'skin_type', newValue)}/>
                            <IngredientRadio text='효과' data={TFData} selectedValue={TFTextChange(item.positivity_status)} handleChange={(newValue) => handleSkinEffectsChange(index, 'positivity_status', newValue)}/>
                            <IngredientInput2 label='효과 설명' onChange={(newValue) => handleSkinEffectChange(index, 'skinDescription', newValue)} value={item.skinDescription}/>
                        </div>                        
                    ))}
                        <button className='dataAddBts' onClick={handleAddSkinTypeFeatures}>추가</button>
                        <button className='dataAddBts' onClick={handleRemoveSkinTypeFeatures}>제거</button>
                    </div>
                </div>
            </div>
            <div className="ingredient_radio_Box2">
                <IngredientRadio text='위험 여부' data={dangerData} selectedValue={dangerTextChange(data.danger_status)} handleChange={handleDangerChange}/>
                <IngredientRadio text= '알레르기 위험 여부'  data={dangerData} selectedValue={dangerTextChange(data.allergy_status)} handleChange={handleADangerChange}/>
                <IngredientRadio text='성분 등급' data={gradeData} selectedValue={String(data.grade)} handleChange={handleGradeChange}/>
                <div className='bts'>
                    <button onClick={handleRevise} className='bt' >{label}</button>
                    {label == '수정' ? (<button onClick={handleDelete} className='bt' >삭제</button>) : (<></>)}
                    <button className='bt' onClick={goBack}>취소</button>
                </div>
            </div>
        </div>
        </div>
    );
};

export default IngredientInfo;
