import React from "react";
import { Text } from "..";
import { Link } from "react-router-dom";

export default function Header2({ ...props }) {
  return (
    <header2
      {...props}
      className={`${props.className} flex flex-col md:items-start justify-end w-[95%] md:w-full gap-7 md:p-5 mt-[-30px]`}
    >
        <ul className="flex gap-[50px] md:gap-5 w-full justify-end mt-[40px]"> 
          <li>
            <a href="#">
            <a href="http://ceprj.gachon.ac.kr:60006/flutter-apk/app-release.apk" download>
              <Text size="3xl" as="p" className="!font-kaushanscript !text-black-900">
                APP 다운로드
              </Text>
            </a>
            </a>
          </li>
          <li> 
            <a href="#">
              <Text size="3xl" as="p" className="!font-kaushanscript !text-black-900">
                <Link to="/login">
                관리자 로그인
                </Link>
                
              </Text>
            </a>
          </li>
        </ul>
      
    </header2>
  );
}
