import React, { useState } from 'react';
import { useTable } from 'react-table';
import '../../css/QandA.css'
import QandAAnswer from 'sidebar/page/QandA/QandA_answer';

// 테이블 컴포넌트
function QandATable({ data, columns }) {  

  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    rows,
    prepareRow,
  } = useTable(
    {
      columns,
      data,
    },
  );

  const [selectedRowData, setSelectedRowData] = useState(null);
  const [isEditing, setIsEditing] = useState(false);
  const [buttonLabel, setbButtonLabel] = useState('');

  const handleEditClick = (e, label) => {
    setSelectedRowData(e);
    setbButtonLabel(label);
    setIsEditing(true);
  };

  const goBack = () => {
    setIsEditing(false);
  };

  

  const trueRows = rows.filter(row => row.original.answer_status === true);
  const falseRows = rows.filter(row => row.original.answer_status === false);

  return (
    <>
      {isEditing ? (<QandAAnswer buttonLabel={buttonLabel} rowData={selectedRowData} goBack={goBack}/>) : (
        <div>
          <div className='QnatableBoxOut'>
          <div className='AnswerTitleBox'>
          <div className='AnswerStatus'></div>
          <div className="AnswerTitle">미답변 문의</div>
          </div>
        <div className='QandAContainer2'>
          <table className='QandAtable' {...getTableProps()}>
            <thead>
              {headerGroups.map(headerGroup => (
                <tr className='columns' {...headerGroup.getHeaderGroupProps()}>
                  {headerGroup.headers.map(column => (
                    <th className='QandAth' {...column.getHeaderProps()}>{column.render('Header')}</th>
                  ))}
                  <th className='QandAth'></th>
                </tr>
              ))}
            </thead>
            <tbody {...getTableBodyProps()}>
              {falseRows.map(row => {
                prepareRow(row);
                return (
                  <tr {...row.getRowProps()}>
                    {row.cells.map(cell => {
                      const content = cell.value === true ? <h2 className='OText'>O</h2> : cell.value === false ? <h2 className='XText'>X</h2> : cell.render('Cell');
                      return <td className='QandAtd' {...cell.getCellProps()}>{content}</td>;
                    })}
                    <td className='QandAtd'>
                      <button className='bt' onClick={() => handleEditClick(row.original, '답변')}>답변</button>
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </div>
          <div className='AnswerTitleBox'>
          <div className='AnswerStatus2'></div>
          <div className="AnswerTitle">답변 문의</div>
          </div>
          <div className='QandAContainer2'>
          <table className='QandAtable' {...getTableProps()}>
            <thead>
              {headerGroups.map(headerGroup => (
                <tr className='columns' {...headerGroup.getHeaderGroupProps()}>
                  {headerGroup.headers.map(column => (
                    <th className='QandAth' {...column.getHeaderProps()}>{column.render('Header')}</th>
                  ))}
                  <th className='QandAth'></th>
                </tr>
              ))}
            </thead>
            <tbody {...getTableBodyProps()}>
              {trueRows.map(row => {
                prepareRow(row);
                return (
                  <tr {...row.getRowProps()}>
                    {row.cells.map(cell => {
                      const content = cell.value === true ? <h2 className='OText'>O</h2> : cell.value === false ? <h2 className='XText'>X</h2> : cell.render('Cell');
                      return <td className='QandAtd' {...cell.getCellProps()}>{content}</td>;
                    })}
                    <td className='QandAtd'>
                      <button className='bt' onClick={() => handleEditClick(row.original, '수정')}>답변</button>
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </div>
        </div>
      </div>
      )}
    </>
  );
}

export default QandATable