import React from 'react';
import '../../css/register_cosmetic.css'

class RegisterRow extends React.Component {
    render() {
        const { data, selectedOption, handleOptionChange } = this.props;

        return (
            <div>
                {data.map((item, index) => (
                    <button
                        key={index}
                        onClick={() => handleOptionChange(item.name)}
                        style={{
                            marginRight: '24px',
                            padding: '6px 26px',
                            borderRadius: '20px',
                            backgroundColor: selectedOption === item.name ? '#FFAFAF' : 'white',
                            border: selectedOption === item.name ? '2px solid white' : '2px solid black',
                            cursor: 'pointer',
                            color: selectedOption === item.name ? 'white' : 'black',
                            fontSize: '14px',
                            fontWeight: 'bold',
                        }}
                    >
                        {item.name}
                    </button>
                ))}
            </div>
        );
    }
}

export default RegisterRow;
