import React from 'react';

const SaleInfoInput = ({ salesInfo, onChange }) => {

    const handleChange = (e) => {
        const { name, value } = e.target;
        onChange({ ...salesInfo, [name]: value });
    };

    return (
        <div className='linkInpuBox'>
            <input
                type="text"
                name="purchase_name"
                placeholder="판매처 이름"
                value={salesInfo.purchase_name}
                onChange={handleChange}
                className='linkInput'
            />
            <div className='linkInputBoxIn'>
            <span>가격 : </span>
            <input
                type="text"
                name="purchase_price"
                placeholder="가격"
                value={salesInfo.purchase_price}
                onChange={handleChange}
                className='linkInput2'
            /><br />
            <span>링크 : </span>
            <input
                type="text"
                name="purchase_url"
                placeholder="링크"
                value={salesInfo.purchase_url}
                onChange={handleChange}
                className='linkInput3'
            />
            </div>
        </div>
    );
};

export default SaleInfoInput;
