import React from 'react';
import { Line } from 'react-chartjs-2';
import {
    Chart as ChartJS,
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
  } from 'chart.js'
  
  ChartJS.register(
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
  )

const EvaluationLineChart = ({ data }) => {
  const chartData = {
    labels: data.map((item) => item.date),
    datasets: [
        {
          data: data.map((item) => item.value),
          borderColor: '#000000',
          borderWidth: 2,
          pointRadius: 2,
        },
      ],
  };

  const options = {
    plugins: {
      legend: {
        display: false,
      },
    },
    scales: {
      x: {
          grid: {
            display: false,
          },
        },
        y: {
          grid: {
            display: false,
          },
          min: 0,
          max: 5,
        },
    },
    elements: {
      bar: {
        borderRadius: 8,
      },
    },
  };

  return (
    <div className='evaluationGraph'>
      <Line data={chartData} options={options} />
    </div>
  );
};

export default EvaluationLineChart;
