import React from 'react';
import { Line } from 'react-chartjs-2';
import {
    Chart as ChartJS,
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
  } from 'chart.js'
  
  ChartJS.register(
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
  )

const LineChart = ({ data }) => {
  const chartData = {
    labels: data.map((item) => item.date),
    datasets: [
        {
          data: data.map((item) => item.activity),
          borderColor: 'black',
          borderWidth: 2,
          pointRadius: 2,
        },
      ],
  };

  const options = {
    plugins: {
      legend: {
        display: false,
      },
    },
    scales: {
      x: {
        grid: {
          display: false,
        },
      },
      y: {
        grid: {
          display: false,
        },
        min: 0,
      },
    },
  };

  return (
    <div style={{ width: '450px', height: '300px' }}>
      <Line data={chartData} options={options} />
    </div>
  );
};

export default LineChart;
