import React from "react";
import '../../css/home.css'



const NumConatiner = ({text, num, image}) => {
    return(
        
        <div className="numcontainer">
            <div>
                <div className="image"><img src={image} alt="이미지" /></div>
            </div>
            <div>
                <div className="text">
                    <div>{text}</div>
                </div>
                <div className="num">
                    <div>{num}</div>
                </div>
            </div>
        </div>
    );
}

export default NumConatiner