import React, {useState} from 'react';
import UserInfoField from './user_info_row';
import '../../css/user_manage.css'
import UserRadio from './user_radio';
import SkinConcernButtons from './skin_concern';
import Swal from 'sweetalert2'

const UserInfo = ({rowData, handleIsEditingChange,  status, setStatus }) => {
  console.log(rowData);
    const [data, setData] = useState(
        rowData
      );

    const genderData = [
        { name: "남자" },
        { name: "여자" },
    ];

    const skinTypeData = [
        { name: "건성" },
        { name: "지성" },
        { name: "민감성" },
    ];

    const skinConcernData = [
        { name: "해당없음" },
        { name: "아토피" },
        { name: "여드름" },
        { name: "각질" },
        { name: "미백잡티" },
        { name: "주름탄력" },
    ];

    const goBack = () => {
      handleIsEditingChange();
    }
    
    const handleNicknameChange = (newNickname) => {
        setData(prevData => ({
          ...prevData,
          nickname: newNickname
        }));
    };

    const handleIdChange = (newId) => {
        setData(prevData => ({
          ...prevData,
          id: newId
        }));
    };

    const handlePwChange = (newPw) => {
        setData(prevData => ({
          ...prevData,
          password: newPw
        }));
    };

    const handleNameChange = (newName) => {
        setData(prevData => ({
          ...prevData,
          name: newName
        }));
    };

    const handleEmailChange = (newEmail) => {
        setData(prevData => ({
          ...prevData,
          email: newEmail
        }));
    };

    const handleBirthChange = (newBirth) => {
        setData(prevData => ({
          ...prevData,
          birth: newBirth
        }));
    };

    const handleAllergieChange = (newAllergie) => {
        setData(prevData => ({
          ...prevData,
          allergy: newAllergie
        }));
    };

    const handleGenderChange = (newGender) => {
        setData(prevData => ({
          ...prevData,
          gender: newGender === '남자' ? 'MALE' : 'FEMALE'
        }));
    };

    const handleSkinTypeChange = (newSkinType) => {
        setData(prevData => ({
          ...prevData,
          skin_type: newSkinType === '건성' ? 'DRY' : newSkinType === '지성' ? 'OILY' : newSkinType === '민감성' ? 'SENSITIVE' : ''
        }));
    };

    const handleSkinConcernChange = (value) => {
      if(value === '해당없음'){
        setData(prevData => ({
          ...prevData,
          skin_concern: ['해당없음']
        }));
      }
      else{
        setData(prevData => ({
          ...prevData,
          skin_concern: [...prevData.skin_concern, value].filter(item => item !== '해당없음')
        }));
      }
    };
    

    const handleUpdateUserInfo = () => {
      fetch('http://ceprj.gachon.ac.kr:60006/api/admin/memeber/udate/' + data.login_id, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(data),
      })
        .then(response => {
          if (response.ok) {
            Swal.fire({
              title: "수정 되었습니다.",
              icon: "success"
            })
            setStatus(1);
            console.log('성공했어');
            goBack();
          } else {
            Swal.fire({
              title: "수정이 실패하였습니다.",
              icon: "success"
            })
            throw new Error('유저 정보 수정에 실패했습니다.');
          }
        })
        .catch(error => {
          console.error('Error:', error);
        });
    };

    const handleDeleteUserInfo = () => {



      fetch('http://ceprj.gachon.ac.kr:60006/api/admin/member/delete/' + data.login_id, {
        method: 'DELETE',
        headers: {
          'Content-Type': 'application/json',
        },
      })
        .then(response => {
          if (response.ok) {
            console.log('성공했어');
          } else {
            throw new Error('유저 정보 수정에 실패했습니다.');
          }
        })
        .catch(error => {
          console.error('Error:', error);
        });


            Swal.fire({
              title: '사용자를 삭제하시겠습니까?',
              icon: 'warning',
              showCancelButton: true,
              confirmButtonText: '삭제',
              cancelButtonText: '취소',
          }).then((res) => {
            if (res.isConfirmed) {
     

              fetch('http://ceprj.gachon.ac.kr:60006/api/admin/member/delete/' + data.login_id, {
                method: 'DELETE',
                headers: {
                  'Content-Type': 'application/json',
                },
              })
                .then(response => {
                  if (response.ok) {
                    Swal.fire({
                      title: "삭제 되었습니다!",
                      icon: "success"
                    });
                    setStatus(1);
                    goBack();

                  } else {
                    Swal.fire({
                      title: "삭제 되었습니다!",
                      icon: "success"
                    });
                    setStatus(1);
                    goBack();
                  }
                })
                .catch(error => {
                  console.error('Error:', error);
                });





            }
          });





      
    };
  return (
    
    <div>
        <div className='infotext'>회원 정보 수정</div>
    <div className='user-info-container'>
      <div className='user-info-container-in'>
        <div className='userInputBox'>
        <UserInfoField label="닉네임" value={data.nickname} onChange={handleNicknameChange} />
          <UserInfoField label="아이디" value={data.login_id} onChange={handleIdChange} />
          <UserInfoField label="비밀번호" value={data.password} onChange={handlePwChange} />
          <UserInfoField label="이름" value={data.name} onChange={handleNameChange} />
          <UserInfoField label="이메일" value={data.email} onChange={handleEmailChange} />
          <UserInfoField label="생년월일" value={data.birth} onChange={handleBirthChange} />
          <UserInfoField label="알레르기" value={data.allergy} onChange={handleAllergieChange} />
        </div>
        <div className='line'>
          <UserRadio text='성별' data={genderData} selectedValue={data.gender === 'MALE' ? '남자' : '여자'} handleChange={handleGenderChange}/>
          <UserRadio text='피부타입' data={skinTypeData} selectedValue={data.skin_type === 'DRY' ? '건성' : data.skin_type === 'OILY' ?  '지성' : data.skin_type === 'SENSITIVE' ? '민감성' : ''} handleChange={handleSkinTypeChange}/>
          <br />
          <SkinConcernButtons  text='피부걱정' data={skinConcernData} selectedValue={data.skin_concern.length == 0 ? '해당없음' : data.skin_concern} handleChange={handleSkinConcernChange}/>
          </div>
          <div className='infobuttons'>
          <button className='bt' onClick={handleUpdateUserInfo}>수정</button>
          <button className='bt' onClick={handleDeleteUserInfo}>삭제</button>
          <button className='bt' onClick={goBack} >취소</button>
        
      </div>
      </div>
    </div>
    </div>
  );
};

export default UserInfo;