import React from "react";

const sizes = {
  "3xl": "text-5xl font-bold md:text-[44px] sm:text-[38px]",
  "2xl": "text-[45px] font-bold md:text-[41px] sm:text-[35px]",
  xl: "text-[40px] font-bold md:text-[38px] sm:text-4xl",
  s: "text-lg font-semibold",
  md: "text-xl font-bold",
  xs: "text-[11px] font-bold",
  lg: "text-2xl font-semibold md:text-[22px]",
};

const Heading = ({ children, className = "", size = "xs", as, ...restProps }) => {
  const Component = as || "h6";

  return (
    <Component className={`text-white-A700 font-roboto ${className} ${sizes[size]}`} {...restProps}>
      {children}
    </Component>
  );
};

export { Heading };
