import React, { useState } from "react";
import "../css/sidebar.css";
import Swal from 'sweetalert2'
import { Link } from "react-router-dom";
import { useNavigate } from 'react-router-dom';
import Home from "Home";

const Sidebar = ({ selectedItem, onItemClick }) =>{
    const navigate = useNavigate();

    const Logout = () =>{


        Swal.fire({
            title: '로그아웃 하시겠습니까?',
            icon: 'warning',
            showCancelButton: true,
            confirmButtonText: '확인',
            cancelButtonText: '취소',
            confirmButtonColor: "#3085d6",
            cancelButtonColor: "#d33",
            }).then((res) => {
                if (res.isConfirmed) {
                Swal.fire({
                    title: "로그아웃 되었습니다.",
                    icon: "info"
                });
                // 세션 스토리지 비우기
                sessionStorage.clear();
                navigate('/');
            }
            else{
                return;
            }
            })

    }




    return (
        <div className="menu">
            <br /><br />
            <Link to="/home">
                <h1 onClick={Home}>intelly cosm</h1>
            </Link>
            <br />
            <button className={selectedItem === "대시보드" ? "selected" : "unselected"} onClick={() => onItemClick("대시보드")}>
                대시보드
            </button>
            <button className={selectedItem === "사용자 관리" ? "selected" : "unselected"} onClick={() => onItemClick("사용자 관리")}>
                사용자 관리
            </button>
            <button className={selectedItem === "화장품 등록" ? "selected" : "unselected"} onClick={() => onItemClick("화장품 등록")}>
                화장품 등록
            </button>
            <button className={selectedItem === "화장품(링크) 관리" ? "selected" : "unselected"} onClick={() => onItemClick("화장품(링크) 관리")}>
                화장품(링크) 관리
            </button>
            <button className={selectedItem === "평가 관리" ? "selected" : "unselected"} onClick={() => onItemClick("평가 관리")}>
                평가 관리
            </button>
            <button className={selectedItem === "화장품 성분 관리" ? "selected" : "unselected"} onClick={() => onItemClick("화장품 성분 관리")}>
                화장품 성분 관리
            </button>
            <button className={selectedItem === "문의 관리" ? "selected" : "unselected"} onClick={() => onItemClick("문의 관리")}>
                문의 관리
            </button>
            <button className={selectedItem === "AI 관리" ? "selected" : "unselected"} onClick={() => onItemClick("AI 관리")}>
                AI 관리
            </button>
            <div className="logout" onClick={Logout}>
                <button>로그아웃</button>
            </div>
        </div>
    );
}

export default Sidebar;
