
import React, { useState, useEffect } from 'react';
import { useTable, useFilters, useGlobalFilter, usePagination } from 'react-table';
import '../../css/register_cosmetic.css'
import { FiSearch } from 'react-icons/fi';
import RadioGroup from './radio_group';
import Register from '../../page/cosmetic_register/register';
import Swal from 'sweetalert2'

function CosmeticTable({ columns, data, status, setStatus  }) {
  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    rows,
    prepareRow,
    setGlobalFilter,
    page, // 페이지로 나뉜 데이터
    canPreviousPage,
    canNextPage,
    pageOptions,
    pageCount,
    gotoPage,
    nextPage,
    previousPage,
    setPageSize,
    state: { pageIndex, pageSize },
  } = useTable(
    {
      columns,
      data,
      initialState: { pageIndex: 0, pageSize: 10 }
    },
    useFilters,
    useGlobalFilter,
    usePagination
  );
  useEffect(() => {
    const handleParams = async () => {
      const modifiedData = data.map(item => ({ ...item, 모두선택: false }));
      let remainingData = [...modifiedData]; // 처리되지 않은 데이터를 복사
  
      for (let i = 0; i < 3; i++) {
        const nameCounts = {};
        let object = remainingData[0];
      
        remainingData.forEach(item => {
          if (item && item.cosmetic_name) { // Null 체크 추가
            nameCounts[item.cosmetic_name] = (nameCounts[item.cosmetic_name] || 0) + 1;
            if (object && object.score < item.score) {
              object = item;
            }
          }
        });
      
        let mostFrequentName = '';
        let maxCount = 0;
        Object.entries(nameCounts).forEach(([name, count]) => {
          if (count > maxCount) {
            maxCount = count;
            mostFrequentName = name;
          }
        });
      
        // 상태에 데이터 추가
        if (mostFrequentName) { // Null 체크 추가
          setMaxNumData(prevData => [...prevData, mostFrequentName]);
        }
        if (object && object.cosmetic_name) { // Null 체크 추가
          setHighScoreData(prevData => [...prevData, object.cosmetic_name]);
        }
      
        // 처리된 데이터에서 최댓값 제거
        remainingData = remainingData.filter(item => item && item.cosmetic_name !== mostFrequentName);
      }
      
    };
  
    handleParams();
  }, [data]);


  useEffect(() => {
    const handleParams = async () => {
      const modifiedData = data.map(item => ({ ...item, 모두선택: false }));
      let remainingData = [...modifiedData];
  
      const uniqueHighScores = new Set(); // 중복되지 않는 평균 평가 점수를 저장할 Set
      const maxScores = 3; // 최대 유지할 평균 평가 점수 개수
  
      while (uniqueHighScores.size < maxScores && remainingData.length > 0) {
        let maxScore = -Infinity;
        let maxScoreName = '';
  
        remainingData.forEach(item => {
          if (item && item.score > maxScore && !uniqueHighScores.has(item.cosmetic_name)) {
            maxScore = item.score;
            maxScoreName = item.cosmetic_name;
          }
        });
  
        // 최대 평균 평가 점수와 그에 해당하는 화장품 이름을 Set에 추가
        if (maxScoreName) {
          uniqueHighScores.add(maxScoreName);
        }
  
        // 처리된 데이터에서 최댓값 제거
        remainingData = remainingData.filter(item => item && item.cosmetic_name !== maxScoreName);
      }
  
      // Set을 배열로 변환하여 highScoreData로 설정
      setHighScoreData(Array.from(uniqueHighScores));
    };
  
    handleParams();
  }, [data]);
  

  
  
  



  
  // 
  const [filterInput, setFilterInput] = useState('');
  const [selectedValue, setSelectedValue] = useState('');
  const [checkBoxValues, setCheckBoxValues] = useState({});
  const [isEditing, setIsEditing] = useState(false);
  const [highScoreData, setHighScoreData] = useState([]);
  const [maxNumData, setMaxNumData] = useState([]);

  const RadioData = maxNumData.map(item => ({ name: item }));
  
  const RadioData2 = highScoreData.map(item => ({ name: item }));

  // const [isAllChecked, setIsAllChecked] = useState(false);

  // 검색
  const handleFilterChange = (e) => {
    const value = e.target.value || undefined;
    setGlobalFilter(value);
    setFilterInput(value);
  };

  // 버튼 검색
  const handleChange = (value) => {
    if (selectedValue === value) {
      setSelectedValue('');
      setGlobalFilter("");
    } else {
      setSelectedValue(value);
      setGlobalFilter(value);
    }
  };

  // 체크박스 값 변경
  const handleCheckBoxChange = (rowId, newValue) => {
    setCheckBoxValues(prevState => ({
      ...prevState,
      [rowId]: newValue
    }));
  };

  // 체크박스 값 전체 변경
  const handleSelectAll = () => {
    console.log(checkBoxValues);
    const updatedCheckBoxValues = {};
    // checkBoxValues.forEach(val =>{
    //   if(val == true){
    //     setIsAllChecked(true);
    //   }
    // });
    // if(isAllChecked){
    //   rows.forEach(row => {
    //     updatedCheckBoxValues[row.id] = false;
    //   });
    // }else{
    //   rows.forEach(row => {
    //     updatedCheckBoxValues[row.id] = true;
    //   });
    // }
    rows.forEach(row => {
      updatedCheckBoxValues[row.id] = true;
    });
    setCheckBoxValues(updatedCheckBoxValues);
  };

  const handleSelectDelete = (e) => {

    console.log(e);
    
    Swal.fire({
      title: '삭제 하시겠습니까?',
      icon: 'info',
      showCancelButton: true,
      confirmButtonText: '등록',
      cancelButtonText: '취소',
    }).then((res) => {
      if (res.isConfirmed) {
        Swal.fire({
          title: '잠시만 기다려주세요...',
          allowOutsideClick: false,
          showConfirmButton: false,
          willOpen: () => {
            Swal.showLoading();
          },
          customClass: {
            popup: 'my-custom-popup-class',
          }
        });
    
        fetch(`http://ceprj.gachon.ac.kr:60006/api/admin/analysis/cosmetic/delete/${e}`, {
          method: 'DELETE',
        })
          .then(data => {
            Swal.close(); // 로딩 화면 닫기
            if (data.hasOwnProperty('error')) {
              Swal.fire({
                title: '삭제 실패 하였습니다.',
                icon: 'error',
              });
            } else {
              Swal.fire({
                title: '삭제 되었습니다',
                icon: 'success',
              });
              setStatus(1);
            }
            console.log(data);
          })
          .catch(error => {
            Swal.close(); // 로딩 화면 닫기
            Swal.fire({
              title: '삭제에 실패 하였습니다.',
              icon: 'error',
            });
            console.error('Error:', error);
          });
      }
    });
}






  
  
  
  const handleSelectRegister = e => {
  
  }
  
  const handleRegister = () => {
    setIsEditing(true);
  };
  
  const handleButtonRegister = (e) => {



    // 서버로 데이터 보내기

    Swal.fire({
      title: '등록 하시겠습니까?',
      icon: 'info',
      showCancelButton: true,
      confirmButtonText: '등록',
      cancelButtonText: '취소',
    }).then((res) => {
      if (res.isConfirmed) {
        Swal.fire({
          title: '잠시만 기다려주세요...',
          allowOutsideClick: false,
          showConfirmButton: false,
          willOpen: () => {
            Swal.showLoading();
          },
          customClass: {
            popup: 'my-custom-popup-class',
          }
        });
    
        fetch(`http://ceprj.gachon.ac.kr:60006/api/admin/cosmetics/add/Auto/${e}`, {
          method: 'POST',
        })
          .then(response => response.json())
          .then(data => {
            Swal.close(); // 로딩 화면 닫기
            if (data.hasOwnProperty('error')) {
              Swal.fire({
                title: '등록에 실패 하였습니다.',
                icon: 'error',
              });
            } else {
              Swal.fire({
                title: '등록 되었습니다',
                icon: 'success',
              });
              setStatus(1);
            }
            console.log(data);
          })
          .catch(error => {
            Swal.close(); // 로딩 화면 닫기
            Swal.fire({
              title: '등록에 실패 하였습니다.',
              icon: 'error',
            });
            console.error('Error:', error);
          });
      }
    });
}
    

  // // 전체 중 체크박스 값 선택된거 확인
  // const getCheckBox = () => {
  //   const checkedCheckboxes = [];
  //   for (const [rowId, isChecked] of Object.entries(checkBoxValues)) {
  //     if (isChecked) {
  //       checkedCheckboxes.push(rowId);
  //     }
  //   }
  // }



  const handleIsEditingChange = () => {
    setIsEditing(false);
  };

  console.log(RadioData);
  console.log(RadioData2);

  return (
    <>
    {isEditing ? (
        <Register handleIsEditingChange={handleIsEditingChange}/>
      ) : (
    <>
      

      <div className='radioGroup'>
        <RadioGroup RadioData={maxNumData.map(item => ({ name: item }))} RadioData2={highScoreData.map(item => ({ name: item }))} selectedValue={selectedValue} handleChange={handleChange} setStatus={setStatus} status={status} />
      </div>
    <div className='tableBox2'>      
      <div className='search-container2'>
        <input
          className='search'
          value={filterInput}
          onChange={handleFilterChange}
          placeholder="검색" />
          <FiSearch className='search-icon' />
      </div>
      <table className='registerTable' {...getTableProps()}>
        <thead>
          {headerGroups.map(headerGroup => (
            <tr className='columns' {...headerGroup.getHeaderGroupProps()}>
              {headerGroup.headers.map(column => (
                <th className='registerth' {...column.getHeaderProps()}>
                  {column.render('Header') === '모두선택' ? (
                    <button className='bt' onClick={handleSelectAll}>모두 선택</button>
                  ) : (
                    column.render('Header')
                  )}
                </th>
              ))}
              <th className='registerth'></th>
            </tr>
          ))}
        </thead>
        <tbody {...getTableBodyProps()}>
          {page.map(row => {
            prepareRow(row);
            return (
              <tr className='registertr' {...row.getRowProps()}>
                {row.cells.map(cell => {
                  return <td className='registertd' {...cell.getCellProps()}>
                    {cell.column.id === 'image_path' && <img className='cImage_R' src={cell.value} alt="이미지" />}
                    {cell.column.id === '모두선택' && <input className='cCheckBox' type="checkbox" checked={checkBoxValues[row.id] || false} onChange={() => handleCheckBoxChange(row.id, !checkBoxValues[row.id])} />}
                    {(cell.column.id !== 'image_path' && cell.column.id !== '모두선택') && cell.render('Cell')}
                  </td>;
                })}
                <td className='registertd'>
                  <div className='registertd_bts_box'>
                  <button className='bt' onClick={() => handleButtonRegister(row.original.id)}>등록</button>
                  <button className='bt' onClick={() => handleSelectDelete(row.original.id)}>삭제</button>
                  </div>
                </td>
              </tr>
            );
          })}
        </tbody>
      </table>

      <div className="pagination">
              <button className='page_button' onClick={() => gotoPage(0)} disabled={!canPreviousPage}>
                {'<<'}
              </button>
              <button className='page_button' onClick={() => previousPage()} disabled={!canPreviousPage}>
                {'<'}
              </button>
              {pageOptions.map((pageOption, i) => (
                <button 
                  key={i}
                  onClick={() => gotoPage(pageOption)}
                  disabled={pageOption === pageIndex}
                  className={pageIndex === pageOption ? 'num_button' : 'num_button2'}
                >
                  {pageOption + 1}
                </button>
              ))}
              <button className='page_button' onClick={() => nextPage()} disabled={!canNextPage}>
                {'>'}
              </button>
              <button className='page_button' onClick={() => gotoPage(pageCount - 1)} disabled={!canNextPage}>
                {'>>'}
              </button>
              
             
              <select className='Select'
                value={pageSize}
                onChange={e => {
                  setPageSize(Number(e.target.value));
                }}
              >
                {[10, 20, 30, 40, 50].map(pageSize => (
                  <option key={pageSize} value={pageSize}>
                    Show {pageSize}
                  </option>
                ))}
              </select>
            </div>
      
      <div className='bts'>
        
        <button className='bt' onClick={handleSelectRegister}>선택 등록</button>
        <button className='bt' onClick={handleRegister} >수동 등록</button>
      </div>
      </div>
    </>
      )}
    </>
  );
}

export default CosmeticTable;
