import React from 'react';
import '../../css/user_manage.css';

class SkinConcernButtons extends React.Component {
  render() {
    const { text, data, selectedValue, handleChange } = this.props;

    // 데이터를 3개씩 묶는 함수
    const chunkArray = (arr, chunkSize) => {
      const chunkedArray = [];
      for (let i = 0; i < arr.length; i += chunkSize) {
        chunkedArray.push(arr.slice(i, i + chunkSize));
      }
      return chunkedArray;
    };

    const isSelected = (name) => {
      return selectedValue.includes(name);
    };

    // 데이터를 3개씩 묶음
    const groupedData = chunkArray(data, 3);
    return (
      <div className='raidOutBox'>
        <div className='skinconcernText'>피부 고민</div>
        <div className='radio_container3_Box'>
        {groupedData.map((group, rowIndex) => (
          
          <div key={rowIndex} className='radio-container3'>
            {group.map((item, colIndex) => (
              <button
                key={colIndex}
                onClick={() => handleChange(item.name)}
                style={{
                  marginLeft: '-5px',
                  marginRight: '10px',
                  padding: '10px 15px',
                  // borderRadius: '12px',
                  cursor:'pointer',
                  backgroundColor: '#FFFFFF',
                  border: isSelected(item.name) ? '2px solid #FFAFAF' : '2px solid rgb(180, 176, 176)',
                  color: isSelected(item.name) ? '#FFAFAF' : 'black',
                  fontSize: '14px',
                  fontWeight: 'bold',
                  height: '45px'
                }}
              >
                {item.name}
              </button>
            ))}
          </div>
         
        ))}
         </div>
      </div>
    );
  }
}

export default SkinConcernButtons;
