import React from 'react';
import '../../css/register_cosmetic.css'

class RadioGroup extends React.Component {
    render() {
        const { RadioData, RadioData2, selectedValue, handleChange } = this.props;
        return (
            <div>
                
                <div className='radio_title1'>많이 분석된 화장품</div>
                <div className='cosRow'>
                {RadioData.map((item, index) => (
                    <button
                        key={index}
                        onClick={() => handleChange(item.name)}
                        style={{
                            marginRight: '20px',
                            padding: '10px 15px',
                            borderRadius: '20px',
                            border: selectedValue === item.name ? '2px solid #FFAFAF' : '2px solid #E1E6F0',
                            cursor: 'pointer',
                            color: selectedValue === item.name ? '#FFAFAF' : '#627183',
                            fontSize:'16px',
                            fontWeight:'bold',
                            
                        }}
                    >
                        {item.name}
                    </button>
                ))}
            </div>
            
                <div className='radio_title2'>평균 평가 점수가 높은 화장품</div>
                <div className='cosRow'>
                {RadioData2.map((item, index) => (
                    <button
                        key={index}
                        onClick={() => handleChange(item.name)}
                        style={{
                            marginRight: '20px',
                            padding: '10px 15px',
                            borderRadius: '20px',
                            border: selectedValue === item.name ? '2px solid #FFAFAF' : '2px solid #E1E6F0',
                            cursor: 'pointer',
                            color: selectedValue === item.name ? '#FFAFAF' : '#627183',
                            fontSize:'16px',
                            fontWeight:'bold',
                        }}
                    >
                        {item.name}
                    </button>
                ))}
            </div>
            </div>
        );
    }
}

export default RadioGroup;
