import React, { useState } from "react";
import '../../css/QandA.css'
import Swal from "sweetalert2";

const QandAAnswer = ({ rowData, goBack, buttonLabel, setIsEditing }) => {
    const [data, setData] = useState(rowData);

    const handleEditClick = (e) => {


      if(buttonLabel === '수정'){
        //= setIsEditing(true);
        // 서버로 데이터 보내기
        console.log(data);
        

            Swal.fire({
              title: '답변을 수정 하시겠습니까?',
              icon: 'warning',
              showCancelButton: true,
              confirmButtonText: '확인',
              cancelButtonText: '취소',
              confirmButtonColor: "#3085d6",
              cancelButtonColor: "#d33",
              }).then((res) => {
                if (res.isConfirmed) {
                  fetch(`http://ceprj.gachon.ac.kr:60006/api/admin/qna/answer2/${data.id}`, {
                  method: 'PUT',
                  body: JSON.stringify(data),
                  headers: {
                      'Content-Type': 'application/json'
                  },
            })
              .then(response => {
                if (response.ok) {
                  Swal.fire({
                    title: "답변이 수정 되었습니다!",
                    icon: "success"
                  });
                  goBack();
                  console.log('성공했어');
                } else {
                  throw new Error('유저 정보 수정에 실패했습니다.');
                }
              })
              .catch(error => {
                console.error('Error:', error);
              });
                    }
              });
          }
          else{
            Swal.fire({
              title: '답변을 등록 하시겠습니까?',
              icon: 'warning',
              showCancelButton: true,
              confirmButtonText: '확인',
              cancelButtonText: '취소',
              confirmButtonColor: "#3085d6",
              cancelButtonColor: "#d33",
              }).then((res) => {
                if (res.isConfirmed) {
                  fetch(`http://ceprj.gachon.ac.kr:60006/api/admin/qna/answer/${data.id}`, {
                  method: 'PUT',
                  body: JSON.stringify(data),
                  headers: {
                      'Content-Type': 'application/json'
                  },
            })
              .then(response => {
                if (response.ok) {
                  Swal.fire({
                    title: "답변이 등록 되었습니다!",
                    icon: "success"
                  });
                  goBack();
                  console.log('성공했어');
                } else {
                  throw new Error('유저 정보 수정에 실패했습니다.');
                }
              })
              .catch(error => {
                console.error('Error:', error);
              });
                    }
              });
          }
}
   
    const handleAnswerChange = (e) => {
        setData(prevData => ({
            ...prevData,
            answer: e.target.value
        }));
    }

    // 삭제 버튼 클릭 핸들러
  const handleDeleteClick = () => {
    Swal.fire({
      title: '답변을 취소 하시겠습니까?',
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: '확인',
      cancelButtonText: '취소',
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      }).then((res) => {
        if (res.isConfirmed) {
          fetch('http://ceprj.gachon.ac.kr:60006/api/admin/qna/delete/' + data.id, {
      method: 'DELETE',
      headers: {
        'Content-Type': 'application/json',
      },
    })
      .then(response => {
        if (response.ok) {
          Swal.fire({
            title: "취소 되었습니다!",
            icon: "success"
          });
          goBack();
          console.log('성공했어');
        } else {
          throw new Error('유저 정보 수정에 실패했습니다.');
        }
      })
      .catch(error => {
        console.error('Error:', error);
      });
            }
      });
  };

    return (
        <div>
        <div className='infotext'>문의 답변</div>
        <div className='QandAContainer'>
            <div className="QandAContentContainer">
                <span className="userIDText">작성자 ID : {data.member.login_id}</span>
                <div className="qnaBox2">
                    <span style={{ fontSize: "24px" }}>문의 내용</span>
                    <div className="answerTextField"><span>{data.description}</span></div>
                </div>
            </div>
            <div className="qnaBox">
                <span style={{ marginTop: "100px", fontSize: "24px" }}>답변</span>
                <div className="answerBox1">
                    <textarea
                        className="answerTextField"
                        value={data.answer}
                        onChange={handleAnswerChange} />
                </div>
                <div className="answerBt">
                    <button onClick={handleEditClick} className="history">{buttonLabel}</button>
                    {buttonLabel === '수정' ? (<button onClick={handleDeleteClick} style={{marginLeft:'24px'}}  className="history">삭제</button>) : (<></>)}
                    <button style={{marginLeft:'24px'}} onClick={goBack} className="history">취소</button>
                </div>
            </div>
        </div>
        </div>
    );
};

export default QandAAnswer;