import React from 'react';
import {Chart, BarElement} from 'chart.js'
import { Bar } from 'react-chartjs-2';

Chart.register(BarElement);

const EvaluationStickGraph = ({ data }) => {
  const chartData = {
    labels: data.map(item => item.label),
    datasets: [
      {

        data: data.map(item => item.value),
        backgroundColor: '#FFAFAF',
        borderColor: '#FFAFAF',
        borderWidth: 0,
      },
    ],
  };

  const options = {
    plugins: {
        legend: {
          display: false,
        },
      },
    indexAxis: 'y',
    scales: {
        x: {
            grid: {
              display: false,
            },
          },
          y: {
            grid: {
              display: false,
            },
          },
      },
      elements: {
        bar: {
          borderRadius: 8,
        },
      },
  };

  return (
    <div className='evaluationGraph'>
      <Bar data={chartData} options={options} />
    </div>
  );
};

export default EvaluationStickGraph;
