import React from 'react';
import '../../css/ingredient.css';

const IngredientInput2 = ({ label, value, onChange }) => {
  
  const handleChange = (e) => {
    onChange(e.target.value);
  };

  return (
    <div className='ingredient_InputBox_box2'>
    <span style={{margin:'25px', }} className='Iradio-label'>{label}</span>
      <input
        type="text"
        value={value}
        onChange={handleChange}
        className="descriptiontextfield"
      />
   </div>
  );
};

export default IngredientInput2;
