import React, { useState, useEffect } from "react"
import '../../css/evaluation.css';
import EvaluationTable from "./evalutation_table";

const Evaluation = () => {
    const [data, setData] = useState([]);
    const [dateData, setDateData] = useState([]);
    const [lineData, setLineData] = useState([]);
    const [lineData2, setLineData2] = useState([]);

    useEffect(() => {
        const fetchData = async () => {
            try {
                // 서버로부터 데이터를 가져오는 API 호출
                const response = await fetch('http://ceprj.gachon.ac.kr:60006/api/admin/evaluation');
                const fetchedData = await response.json();
                setData(fetchedData);
                console.log("데이터 : " + fetchedData);

                if (fetchedData && fetchedData.average && fetchedData.average.length > 0 && fetchedData.average[0].contents) {
                    const { start_date, average, dat_count } = fetchedData.average[0].contents;
                    const data1 = fetchedData.average[1].contents;

                    const startDate = new Date(start_date);
                    const lineDataArray = [];

                    const startDate2 = new Date(data1.start_date);
                    const lineDataArray2 = [];

                    for (let i = 0; i < dat_count; i++) {
                        const currentDate = new Date(startDate);
                        currentDate.setDate(startDate.getDate() + i);
                        const formattedDate = currentDate.toISOString().split('T')[0];
                        const value = average[i];
                        lineDataArray.push({ date: formattedDate, value: value });
                    }

                    for (let i = 0; i < data1.dat_count; i++) {
                        const currentDate = new Date(startDate2);
                        currentDate.setDate(startDate2.getDate() + i);
                        const formattedDate = currentDate.toISOString().split('T')[0];
                        const value = data1.average[i];
                        lineDataArray2.push({ date: formattedDate, value: value });
                    }

                    console.log(lineDataArray);
                    console.log(lineDataArray2);
                    setLineData(lineDataArray);
                    setLineData2(lineDataArray2);
                }
            } catch (error) {
                console.error('Error fetching data:', error);
            }
        };

        fetchData();
    }, []);

    const evalData = [
        { "label": "5", "value": data.length == 0 ? '' : data.rate[0].five_rate, },
        { "label": "4", "value": data.length == 0 ? '' : data.rate[0].four_rate, },
        { "label": "3", "value": data.length == 0 ? '' : data.rate[0].three_rate, },
        { "label": "2", "value": data.length == 0 ? '' : data.rate[0].two_rate, },
        { "label": "1", "value": data.length == 0 ? '' : data.rate[0].one_rate, },
    ]

    const evalData2 = [
        { "label": "5", "value": data.length == 0 ? '' : data.rate[1].five_rate, },
        { "label": "4", "value": data.length == 0 ? '' : data.rate[1].four_rate, },
        { "label": "3", "value": data.length == 0 ? '' : data.rate[1].three_rate, },
        { "label": "2", "value": data.length == 0 ? '' : data.rate[1].two_rate, },
        { "label": "1", "value": data.length == 0 ? '' : data.rate[1].one_rate, },
    ]


    return (
        <>
            <EvaluationTable lineData={lineData} lineData2={lineData2} evalData={evalData} evalData2={evalData2} />
        </>
    );
}

export default Evaluation