import React, {useState} from 'react';
import { useTable, useFilters, useGlobalFilter, usePagination } from 'react-table';
import '../../css/cosmetic_link.css'
import CosmeticInfo from '../../page/cosmetic_link/cosmetic_info';
import Swal from 'sweetalert2'
import { FiSearch } from 'react-icons/fi';

// 테이블 컴포넌트
function LinkTable({ columns, data, status, setStatus  }) {
  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    setGlobalFilter,
    rows,
    prepareRow,
    page, // 페이지로 나뉜 데이터
    canPreviousPage,
    canNextPage,
    pageOptions,
    pageCount,
    gotoPage,
    nextPage,
    previousPage,
    setPageSize,
    state: { pageIndex, pageSize },
  } = useTable(
    {
      columns,
      data,
      initialState: { pageIndex: 0, pageSize: 10 }
    },
    useFilters,
    useGlobalFilter,
    usePagination
  );

  // 상태: 수정 중 여부
  const [isEditing, setIsEditing] = useState(false);
  // 선택한 행의 데이터 상태
  const [selectedRowData, setSelectedRowData] = useState(null);
  // 검색 텍스트
  const [filterInput, setFilterInput] = useState('');

    // 검색
    const handleFilterChange = (e) => {
      const value = e.target.value || undefined;
      setGlobalFilter(value);
      setFilterInput(value);
    };

  // 수정 버튼 클릭 핸들러
  const handleEditClick = (rowData) => {
    setIsEditing(true);
    setSelectedRowData(rowData);
    console.log(rowData);
  };

  const goBack = () => {
    setIsEditing(false);
  };

  const handleSelectDelete = (rowData) => {


    
    Swal.fire({
      title: '삭제 하시겠습니까?',
      icon: 'info',
      showCancelButton: true,
      confirmButtonText: '등록',
      cancelButtonText: '취소',
    }).then((res) => {
      if (res.isConfirmed) {
        Swal.fire({
          title: '잠시만 기다려주세요...',
          allowOutsideClick: false,
          showConfirmButton: false,
          willOpen: () => {
            Swal.showLoading();
          },
          customClass: {
            popup: 'my-custom-popup-class',
          }
        });
    
        fetch(`http://ceprj.gachon.ac.kr:60006/api/admin/cosmetic/delete/${rowData.id}`, {
          method: 'DELETE',
        })
          .then(data => {
            Swal.close(); // 로딩 화면 닫기
            if (data.hasOwnProperty('error')) {
              Swal.fire({
                title: '삭제 실패 하였습니다.',
                icon: 'error',
              });
            } else {
              Swal.fire({
                title: '삭제 되었습니다',
                icon: 'success',
              });
              setStatus(1);
            }
            console.log(data);
          })
          .catch(error => {
            Swal.close(); // 로딩 화면 닫기
            Swal.fire({
              title: '삭제에 실패 하였습니다.',
              icon: 'error',
            });
            console.error('Error:', error);
          });
      }
    });
}



  return (
    <>
    {isEditing ? (
        <CosmeticInfo goBack={goBack} rowData={selectedRowData} status={status} setStatus={setStatus}/>
      ) : (
    <>
    <div className='linktableBoxOut'>
      <table className='linktable' {...getTableProps()}>
      <div className='search-container2'>
        <input
          className='search'
          value={filterInput}
          onChange={handleFilterChange}
          placeholder="검색" />
          <FiSearch className='search-icon' />
      </div>
        <thead>
          {headerGroups.map(headerGroup => (
            <tr className='columns' {...headerGroup.getHeaderGroupProps()}>
              {headerGroup.headers.map(column => (
                <th className='linkth' {...column.getHeaderProps()}>{column.render('Header')}</th>
              ))}
              <th className='linkth'></th>
            </tr>
          ))}
        </thead>
        <tbody {...getTableBodyProps()}>
          {page.map(row => {
            prepareRow(row);
            return (
                <tr className='columns2' {...row.getRowProps()}>
                {row.cells.map(cell => {
                  return <td className='linktd'{...cell.getCellProps()}>
                    {cell.column.id === 'image_path' && <img className='cImage' src={cell.value} alt="이미지" />}
                    {(cell.column.id !== 'image_path') && cell.render('Cell')}
                  </td>;
                })}
                <td className='linktd'>
                  <div className='Link_bts_Box'>
                  <button className='bt' onClick={() => handleEditClick(row.original)}>관리</button>
                  <button className='bt' onClick={() => handleSelectDelete(row.original)}>삭제</button>
                  </div>
                </td>
              </tr>
            );
          })}
        </tbody>
      </table>
      <div className="pagination">
              <button className='page_button' onClick={() => gotoPage(0)} disabled={!canPreviousPage}>
                {'<<'}
              </button>
              <button className='page_button' onClick={() => previousPage()} disabled={!canPreviousPage}>
                {'<'}
              </button>
              {pageOptions.map((pageOption, i) => (
                <button 
                  key={i}
                  onClick={() => gotoPage(pageOption)}
                  disabled={pageOption === pageIndex}
                  className={pageIndex === pageOption ? 'num_button' : 'num_button2'}
                >
                  {pageOption + 1}
                </button>
              ))}
              <button className='page_button' onClick={() => nextPage()} disabled={!canNextPage}>
                {'>'}
              </button>
              <button className='page_button' onClick={() => gotoPage(pageCount - 1)} disabled={!canNextPage}>
                {'>>'}
              </button>
              
             
              <select className='Select'
                value={pageSize}
                onChange={e => {
                  setPageSize(Number(e.target.value));
                }}
              >
                {[10, 20, 30, 40, 50].map(pageSize => (
                  <option key={pageSize} value={pageSize}>
                    Show {pageSize}
                  </option>
                ))}
              </select>
            </div>
    </div>
    </>
      )}
    </>
  );
}

export default LinkTable
