import React from 'react';

class IngredientRadio extends React.Component {
    render() {
        const { text, data, selectedValue, handleChange } = this.props;
        return (
            <div className='Iradio-container'>
                <span className='Iradio-label'>{text}</span>
                <div className='Iradiobts'>
                {data.map((item, index) => (
                    <button
                        key={index}
                        onClick={() => handleChange(item.name)}
                        style={{
                            marginRight: '10px',
                            marginBottom: '5px',
                            padding: '10px 15px',
                            borderRadius: '12px',
                            backgroundColor: '#FFFFFF',
                            border: selectedValue === item.name ? '2px solid #FFAFAF' : '2px solid rgb(180, 176, 176)',
                            cursor: 'pointer',
                            color: selectedValue === item.name ? '#FFAFAF' : 'black',
                            fontSize:'14px',
                            fontWeight:'bold',
                        }}
                    >
                        {item.name}
                    </button>
                ))}
                </div>
            </div>
        );
    }
}

export default IngredientRadio;
