import React, { useState, useEffect } from "react"
import '../../css/evaluation.css';
import EvaluationLineChart from "sidebar/component/evaluation/line_graph";
import EvaluationStickGraph from "sidebar/component/evaluation/stcik_graph";

const EvaluationHistory = ({ handleHistory, historyText }) => {
    const [data, setData] = useState([]);

    useEffect(() => {
      const fetchData = async () => {
        try {
          // 서버로부터 데이터를 가져오는 API 호출
          const response = await fetch('http://ceprj.gachon.ac.kr:60006/api/admin/evaluation/history');
          const fetchedData = await response.json();
          
          console.log(fetchedData);
          if(historyText === 'OCR'){
            setData(fetchedData.OCR);
            console.log(fetchedData.OCR)
          }else{
            setData(fetchedData.recommend);
            console.log(fetchedData.recommend)
          }
          console.log(data);          
        } catch (error) {
          console.error('Error fetching data:', error);
        }
      };

      fetchData();
    }, []);

    return (
        <div>
            <div style={{paddingBottom:'0px'}} className='titleBox'>
            
                <div className="title">History</div>
                <div className="title44">(AI 버전별 기록)</div>
                <hr className="n1" />
                <div className="E_bts_Box">
                <button onClick={handleHistory} className='bt' >뒤로가기</button>
                </div>
            </div>
            <div className="evaluation-container2">
                {data.map((item, index) => (
                    <div key={index} className="graphRow">
                        <div style={{ marginRight: "80px" }}>
                            <h2 className="versionText">버전 {item.version}</h2><br /><br />
                            <EvaluationLineChart data={item.average.map((value, i) => ({
                                date: `23.01.0${i + 1}`,
                                value: value
                            }))} />
                        </div>
                        <div style={{ marginTop: "90px" }} className="graphContainer">
                            <EvaluationStickGraph data={item.rate.map((value, i) => ({
                                label: `${i + 1}`,
                                value: value
                            }))} />
                        </div>
                    </div>
                ))}
            </div>
        </div>
    );
}

export default EvaluationHistory;