import React, { useState } from "react";
import { Helmet } from "react-helmet";
import { Button, Heading, Input, Img, Text } from "../components/login";
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import { Link } from "react-router-dom";
import Swal from 'sweetalert2'

export default function Signup() {

    const navigate = useNavigate();

    const handleButtonClick = () => {
    navigate('/login');
    };

    const [code, setCode] = useState("");
    const [name, setName] = useState("");
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [re_password, setRe_password] = useState("");
    const [error, setError] = useState("");

    const onChange = (e) => {
    if (!e || !e.target) {
        console.error('Invalid event object:', e);
        return;
    }

    const { name, value } = e.target; 
    if (name === "email") {
        setEmail(value); 
        console.log(value);
    } else if (name === "password") {
        setPassword(value);
    } else if (name == "name"){
        setName(value);
    } else if (name == "code"){
        setCode(value);
        console.log(code);
    } else if (name == "re_password"){
        setRe_password(value);
    }
    };

    const handleSubmit = async (e) => {
    e.preventDefault();

    console.log(code);

    if (email.trim() === "" || password.trim() === "" || name.trim === "" || re_password.trim === "" || code.trim === "") {
      
        Swal.fire({
          title: "모두 입력해주세요",
          icon: "error"
        })
        return;
    }
    if ( re_password != password){
        Swal.fire({
          title: "비밀번호가 서로 다릅니다",
          icon: "error"
        })
        return;
    }

    try {
     
        const response = await axios.post('http://ceprj.gachon.ac.kr:60006/api/admin/signup', {
            name,
            email,
            password,
            code
        });


        const data = response;
        
        if(data.status == 400){
          Swal.fire({
            title: "입력한 코드가 잘못되었습니다. 다시 확인해주세요.",
            icon: "error"
          })
            return;
        }

    
        if (data.status === 200) {
     
          if (data.results && data.results.length > 0) {
              const authId = data.results[0].id; 
              Swal.fire({
                title: "관리자 등록이 완료되었습니다",
                icon: "success"
              })
              navigate('/');
          } else {
            Swal.fire({
              title: "관리자 등록이 완료되었습니다",
              icon: "success"
            })
            navigate('/');
              console.error('로그인 오류: 서버 응답에 auth 속성이 없습니다.');
          }
          } else {
          
          setError(data.message);
          }
    } catch (error) {
        console.error('로그인 오류:', error.response.status);
        if(error.response.status == 400){
          Swal.fire({
            title: "입력한 코드가 잘못되었습니다. 다시 확인해주세요.",
            icon: "error"
          })
        }
            
        
    }
    };

  return (
    <>
      <Helmet>
        <title>Login - Intelly Cosm</title>
        <meta
          name="description"
          content="Access your Intelly Cosm account by logging in. Enter your email and password to continue. Register if you're a new user."
        />
      </Helmet>

      <div className="flex w-full flex-col items-center justify-center gap-[75px] bg-gray-50 px-[54px] pb-[78px] pt-[54px] md:gap-14 md:p-5 sm:gap-[37px]" style={{ backgroundImage: 'url(/images/login/background3.jpg)', backgroundSize: 'cover', backgroundRepeat: 'no-repeat', backgroundPosition: 'center' }}>

        <div className="flex flex-col items-end">
          <Text as="p" className="text-shadow-ts md:mr-0">
          <Link to="/">
            intelly cosm
            </Link>
          </Text>
        </div>

        <div className="w-[600px] md:w-full">
          <div className="mt-[75px] flex flex-col items-start justify-center rounded-[30px] bg-white-A700 pb-[81px] pl-[53px] pr-8 pt-[51px] shadow-xs md:py-5 md:pl-5 sm:p-5">
            <a href="Login" target="_blank" rel="noreferrer" className="ml-[180px]">
              <Heading size="md" as="h1" className="!text-black-900">
                Signup
              </Heading>
            </a>

            <Heading size="s" as="h2" className=" !text-pink-100 shadow-bs md:ml-0 ml-[157px]">
              Welcome to Signup
            </Heading>
            <form onSubmit={handleSubmit}>
              <div className="mt-[46px] flex flex-col ">
              <div className=" flex flex-col gap-[11px]  ">
                  <Heading as="h3" className="ml-[29px] md:ml-0">
                    name*
                  </Heading>
                  <Input
                    shape="round"
                    type="text"
                    name="name"
                    value={name}
                    placeholder={`Enter name`}
                    onChange={onChange}
                    prefix={<Img src="images/login/image 556.png" alt="image 556" className="h-[25px] w-[25px] ml-[10px] mr-[10px] " />}
                  />
                </div>
                <div className=" flex flex-col gap-[11px]  ">
                  <Heading as="h3" className="ml-[29px] md:ml-0">
                    Email*
                  </Heading>
                  <Input
                    shape="round"
                    type="email"
                    name="email"
                    value={email}
                    placeholder={`Enter email`}
                    onChange={onChange}
                    prefix={<Img src="images/login/img_image_553.png" alt="image 553" className="h-[45px] w-[45px]" />}
                  />
                </div>
                <div className="mt-[17px] flex flex-col items-start gap-[11px] self-stretch">
                  <Heading as="h4" className="ml-[29px] md:ml-0">
                    Password*
                  </Heading>
                  <Input
                    shape="round"
                    type="password"
                    name="password"
                    value={password}
                    onChange={onChange}
                    placeholder={`Enter password`}
                    prefix={<Img src="images/login/img_image_555.png" alt="image 555" className="h-[40px] w-[40px]" />}
                    className="gap-0.5"
                  />
                </div>
                <div className="mt-[17px] flex flex-col items-start gap-[11px] self-stretch">
                  <Heading as="h4" className="ml-[29px] md:ml-0">
                    Password*
                  </Heading>
                  <Input
                    shape="round"
                    type="password"
                    name="re_password"
                    value={re_password}
                    onChange={onChange}
                    placeholder={`Re-entering the password`}
                    prefix={<Img src="images/login/img_image_555.png" alt="image 555" className="h-[40px] w-[40px]" />}
                    className="gap-0.5"
                  />
                </div>
                <div className="mt-[17px] flex flex-col items-start gap-[11px] self-stretch">
                  <Heading as="h4" className="ml-[29px] md:ml-0">
                    Code*
                  </Heading>
                  <Input
                    shape="round"
                    type="password"
                    name="code"
                    value={code}
                    onChange={onChange}
                    placeholder={`Enter code`}
                    prefix={<Img src="images/login/image 557.png" alt="image 555" className="h-[40px] w-[40px] mr-[10px]" />}
                    className="gap-0.5"
                  />
                </div>
                
              </div>
              <div className="mt-[46px] flex flex-col items-end justify-start self-stretch">
                <Button shape="round" className="mt-[61px] w-full" style={{ color: 'white' }} onClick={handleSubmit}>
                  SignUp
                </Button>
              </div>
            </form>
            <div className=" flex flex-col items-end justify-start self-stretch">
              <Button shape="round" className="mt-[61px] w-full" style={{ color: 'white' }} onClick={handleButtonClick}>
                Back
              </Button>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
