import React, { useState, useEffect, useRef } from "react";
import { Helmet } from "react-helmet";
import { Button, Heading, Input, Img, Text } from "../components/login";
import axios from 'axios';
import { Link } from 'react-router-dom';
import { useNavigate } from 'react-router-dom';
import Swal from 'sweetalert2'
import { motion } from 'framer-motion';

export default function Login() {

  const ref = useRef(null);
  const [inView, setInView] = useState(false);

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        setInView(entry.isIntersecting);
      },
      {
        threshold: 0.1
      }
    );

    if (ref.current) {
      observer.observe(ref.current);
    }

    return () => {
      if (ref.current) {
        observer.unobserve(ref.current);
      }
    };
  }, []);


  const handleButtonClick_F = () => {
    navigate('/find_password');
    };
    const handleButtonClick_S = () => {
      navigate('/signup');
      };

  const navigate = useNavigate();
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");

  const onChange = (e) => {
    if (!e || !e.target) {
      console.error('Invalid event object:', e);
      return;
    }
  
    const { name, value } = e.target; 
    if (name === "email") {
      setEmail(value); 
      console.log(value);
    } else if (name === "password") {
      setPassword(value);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    console.log(e);
    
    if (email.trim() === "" || password.trim() === "") {
      setError("이메일과 비밀번호를 모두 입력하세요.");
      return;
    }
    
  
    try {

      const response = await axios.post('http://ceprj.gachon.ac.kr:60006/api/admin/login', {
        email,
        password,
      },{withCredentials:true} 
      
      );

      const s2 = response.headers['set-cookies'];
      const s3 = response.headers['set-cookie'];

      console.log(s2);
      console.log(s3);
      

     
    
      const sessionId = response.data.sessionId;
      const session1 = response.data.session;

      console.log(sessionId);
      console.log(session1);
      const session = { sessionId : sessionId, session1 : session1};
      sessionStorage.setItem('session', JSON.stringify(session));
      

      console.log("리스폰스 ===" +response);


        if (response.status === 200) {
          Swal.fire({
            title: "로그인 되었습니다.",
            icon: "success"
          })
          
          navigate("/home");
        } else if (response.status === 404){
          Swal.fire({
            title: "등록되지 않은 아이디입니다",
            icon: "error"
          })
        } else if (response.status === 403){
          Swal.fire({
            title: "비밀번호가 틀렸습니다",
            icon: "error"
          })
        }
      } catch (error) {
        console.error('로그인 오류:', error);
      if (error.response.status === 404){
        Swal.fire({
          title: "등록되지 않은 아이디입니다",
          icon: "error"
        })
        } else if (error.response.status === 403){
          Swal.fire({
            title: "비밀번호가 틀렸습니다",
            icon: "error"
          })
        }
      }
  };

  return (
    <>
      <Helmet>
        <title>Login - Intelly Cosm</title>
        <meta
          name="description"
          content="Access your Intelly Cosm account by logging in. Enter your email and password to continue. Register if you're a new user."
        />
      </Helmet>

      <div className="flex w-full flex-col items-center justify-center gap-[75px] bg-gray-50 px-[54px] pb-[78px] pt-[54px] md:gap-14 md:p-5 sm:gap-[37px]" style={{ backgroundImage: 'url(/images/login/background3.jpg)', backgroundSize: 'cover', backgroundRepeat: 'no-repeat', backgroundPosition: 'center' }}>

        <div className="flex flex-col items-end">
          <Text as="p" className="text-shadow-ts md:mr-0">
            <Link to="/">
            intelly cosm
            </Link>
          </Text>
        </div>

        <motion.div initial={{ opacity: 0, y: 100 }} animate={{ opacity: 1, y: 0 }} transition={{ duration: 0.5 }}>
        <div className="w-[600px] md:w-full">
          <div className="mt-[75px] flex flex-col items-start justify-center rounded-[30px] bg-white-A700 pb-[81px] pl-[53px] pr-8 pt-[51px] shadow-xs md:py-5 md:pl-5 sm:p-5">
            <a href="Login" target="_blank" rel="noreferrer" className="ml-[190px]">
              <Heading size="md" as="h1" className="!text-black-900">
                Login
              </Heading>
            </a>

            <Heading size="s" as="h2" className=" !text-pink-100 shadow-bs md:ml-0 ml-[157px]">
              Login to continue
            </Heading>
            <form onSubmit={handleSubmit}>
              <div className="mt-[46px] flex flex-col ">
                <div className=" flex flex-col gap-[11px]  ">
                  <Heading as="h3" className="ml-[29px] md:ml-0">
                    Email*
                  </Heading>
                  <Input
                    shape="round"
                    type="email"
                    name="email"
                    value={email}
                    placeholder={`Enter email`}
                    onChange={onChange}
                    prefix={<Img src="images/login/img_image_553.png" alt="image 553" className="h-[45px] w-[45px]" />}
                  />
                </div>
                <div className="mt-[17px] flex flex-col items-start gap-[11px] self-stretch">
                  <Heading as="h4" className="ml-[29px] md:ml-0">
                    Password*
                  </Heading>
                  <Input
                    shape="round"
                    type="password"
                    name="password"
                    value={password}
                    onChange={onChange}
                    placeholder={`Enter password`}
                    prefix={<Img src="images/login/img_image_555.png" alt="image 555" className="h-[40px] w-[40px]" />}
                    className="gap-0.5"
                  />
                </div>
              </div>
              <div className=" flex flex-col items-end justify-start self-stretch">
                <Button shape="round" className="mt-[61px] w-full" style={{ color: 'white' }} type="submit">
                  Login
                </Button>
              </div>
            </form>
            <div className="flex flex-col items-end justify-start self-stretch">
              <Button shape="round" className="mt-[61px] w-full" style={{ color: 'white' }} onClick={handleButtonClick_F}>
                Find Password
              </Button>
              <Button shape="round" className="mt-5 w-full" style={{ color: 'white' }} onClick={handleButtonClick_S}>
                Register
              </Button>
            </div>
          </div>
        </div>
        </motion.div>
      </div>
    </>
  );
}
