import React from "react";
import { Text, Img } from "..";

export default function Header({ ...props }) {
  return (
    <header
      {...props}
      className={`${props.className} flex flex-col items-center md:items-start w-[50%] md:w-full mr-auto ml-auto md:mr-0 md:ml-0 gap-7 md:p-5 mt-[-30px]`}
    >
      <div className="flex flex-col items-center md:flex-row md:items-start md:justify-between w-full"> {/* 수정된 부분 */}
        <Img src="images/home/img_header_logo.png" alt="logo image" className="h-[102px] w-[333px] object-contain" />
        </div>
        
      
    </header>
  );
}
