import React, { useState, useRef } from 'react';
import '../../css/register_cosmetic.css';
import RegisterRow from 'sidebar/component/register_cosmetic/register_row';
import SaleInfoInput from 'sidebar/component/register_cosmetic/sales_info_input';
import axios from 'axios';
import Swal from 'sweetalert2'

const Register = ({handleIsEditingChange}) => {
    const [cosmeticData, setCosmeticData] = useState([]);
    const [name, setName] = useState('');
    const [Ingredient, setIngredient] = useState([]);
    const [selectedOption, setSelectedOption] = useState('');
    const [selectedOption2, setSelectedOption2] = useState('');
    const [selectedFile, setSelectedFile] = useState(null);
    const [selectedFilepre, setSelectedFilepre] = useState(null);
    const [selectedIFile, setSelectedIFile] = useState(null);
    const [selectedIFilepre, setSelectedIFilepre] = useState(null);
    const fileInputRef = useRef(null);
    const fileInputRef2 = useRef(null);
    const [salesInfos, setSalesInfos] = useState([{ purchase_name: '', purchase_url: '', purchase_price: '' }]);

    const handleRegister = () => {
        
        Swal.fire({
            title: '등록 하시겠습니까',
            icon: 'info',
            showCancelButton: true,
            confirmButtonText: '확인',
            cancelButtonText: '취소',
            }).then((res) => {
              if (res.isConfirmed) {

                Swal.fire({
                    title: '잠시만 기다려주세요...',
                    allowOutsideClick: false,
                    showConfirmButton: false,
                    willOpen: () => {
                      Swal.showLoading();
                    },
                    customClass: {
                      popup: 'my-custom-popup-class',
                    }
                  });

                console.log(Ingredient);
                
        
                // FormData 객체 생성
                const formData = new FormData();
                // 성분 데이터
                
                // const ingredientsList = Ingredient.split(',')
                // const ingredientsList = Ingredient.split(',').map(ingredient => ingredient.trim());
                //구매처 데이터
                const cosmeticPurchase = {};
                salesInfos.forEach(item => {
                cosmeticPurchase[item.purchase_name] = [item.purchase_url, item.purchase_price];
                });

                console.log("III : " + Ingredient);
                
                const requestData = {
                    cosmetic_name: name,
                    cosmetic_ingredient: Ingredient,
                    cosmetic_purchase: cosmeticPurchase
                };

                console.log(requestData);
                

                
                formData.append('file', selectedFile);
                const jsonBlob = new Blob([JSON.stringify(requestData)], { type: 'application/json' });
                formData.append('request', jsonBlob);
                console.log("후" +selectedFile);

                axios.post('http://ceprj.gachon.ac.kr:60006/api/admin/cosmetics/add/passive', formData, {
                    headers: {
                        'Content-Type': 'multipart/form-data'
                    }
                })
                
                .then(data => {
                        Swal.close(); // 로딩 화면 닫기
                        if (data.hasOwnProperty('error')) {
                          Swal.fire({
                            title: '등록에 실패 하였습니다.',
                            icon: 'error',
                          });
                        } else {
                          Swal.fire({
                            title: '등록 되었습니다',
                            icon: 'success',
                          });
                          goBack();
                        }
                        console.log(`데이터 : ${data}`);
                        console.log(`데이터의 데이터 : ${data.data}`);
                      })
                      .catch(error => {
                        Swal.close(); // 로딩 화면 닫기
                        Swal.fire({
                          title: '등록에 실패 하였습니다.',
                          icon: 'error',
                        });
                        console.error('Error:', error);
                      });        
    };
            });
}

    const handleAddSaleInfo = () => {
        setSalesInfos([...salesInfos, { name: '', link: '', price: '' }]);
    };

    const handleRemoveSaleInfo = (index) => {
        const updatedSalesInfos = [...salesInfos];
        updatedSalesInfos.splice(index, 1); 
        setSalesInfos(updatedSalesInfos); 
    };

    const handlePurchaseChange = (index, updatedInfo) => {
        const newSalesInfos = [...salesInfos];
        newSalesInfos[index] = updatedInfo;
        setSalesInfos(newSalesInfos);
    };

    const handleChange = (e) => {
        setName(e.target.value);
    };

    const handleIngredientChange = (e) => {
        // setIngredient(e.target.value);
        // console.log(Ingredient);
        const ingredientsList = e.target.value.split(',');
        setIngredient(ingredientsList);
    };

    // 라디오 버튼을 클릭할 때 호출되는 핸들러 함수
    const handleOptionChange = async (event) => {
        if (selectedOption === event) {
            setSelectedOption('');
        } else {
            if(salesInfos.length >= 2 || name == ''){
                setSelectedOption(event);
            }else{
                if(event == '자동'){
                        
                    setSelectedOption(event);
                    try {
                        const response = await fetch(`http://ceprj.gachon.ac.kr:60006/api/admin/cosmetic/crawling/${name}`);
                        const fetchedData = await response.json();
                        setSalesInfos(fetchedData.listCrawling);
                    } catch (error) {
                        console.error('Error fetching data:', error);
                    }
                }
                setSelectedOption(event);
            }
        }
    };

    const handleOptionChange2 = (event) => {
        if (selectedOption2 === event) {
            setSelectedOption2('');
        } else {
            setSelectedOption2(event);
        }
    };

    const handleImageReset = () => {
        setSelectedIFile(null);
    };
    
    

    const handleFileInputClick = () => {
        // 파일 선택 input 요소를 클릭하여 파일 선택 다이얼로그를 엽니다.
        fileInputRef.current.click();
    };

        // const handleFileChange = useCallback((e) => {
        //     if (e.target.files === null) return;
    
        //     if (e.target.files[0]) {
        //         setSelectedFile(e.target.files[0]);
        //     }
        // }, []);

    // 파일을 선택할 때 호출되는 핸들러 함수
    const handleFileChange = (event) => {
        const file = event.target.files[0];
        console.log("전"+file);
        if (file) {
           
            setSelectedFile(file);
            const reader = new FileReader();

            reader.onloadend = () => {
                setSelectedFilepre(reader.result);
            };

            reader.readAsDataURL(file);
           
        }
    };


    const handleFileInputClick2 = () => {
        // 파일 선택 input 요소를 클릭하여 파일 선택 다이얼로그를 엽니다.
        fileInputRef2.current.click();
    };

    // 파일을 선택할 때 호출되는 핸들러 함수
    const handleIFileChange = (event) => {
        const file = event.target.files[0];
        
        if (file) {
           
            setSelectedIFile(file);
            const reader = new FileReader();

            reader.onloadend = () => {
                setSelectedIFilepre(reader.result);
            };

            reader.readAsDataURL(file);
           
        }
}

    const fetchImageServer = async () => {


        Swal.fire({
            title: '이미지 성분추출을 하시겠습니까',
            icon: 'info',
            showCancelButton: true,
            confirmButtonText: '확인',
            cancelButtonText: '취소',
            }).then((res) => {
              if (res.isConfirmed) {

                Swal.fire({
                    title: '잠시만 기다려주세요...',
                    allowOutsideClick: false,
                    showConfirmButton: false,
                    willOpen: () => {
                      Swal.showLoading();
                    },
                    customClass: {
                      popup: 'my-custom-popup-class',
                    }
                  });
                    const formData = new FormData();
                    console.log(selectedIFile);
                    formData.append('file', selectedIFile);
                    console.log(selectedIFile);
                    console.log(formData);
                    for (const pair of formData.entries()) {
                        console.log(pair[0]); // 이름 출력
                        console.log(pair[1]); // 값 출력
                    }
                    axios.post('http://ceprj.gachon.ac.kr:60006/api/admin/cosmetic/ingredient/ocr', formData, {
                    headers: {
                        'Content-Type': 'multipart/form-data'
                    }
                    })
                    .then(data => {
                        Swal.close(); // 로딩 화면 닫기
                        if (data.hasOwnProperty('error')) {
                          Swal.fire({
                            title: '등록에 실패 하였습니다.',
                            icon: 'error',
                          });
                        } else {
                          Swal.fire({
                            title: '추출 되었습니다',
                            icon: 'success',
                          });
                        }
                        console.log(data);
                        console.log(`데이터 : ${data}`);
                        console.log(`데이터의 데이터 : ${data.data}`);
                        setIngredient(data.data);
                        console.log(Ingredient);
                      })
                      .catch(error => {
                        Swal.close(); // 로딩 화면 닫기
                        Swal.fire({
                          title: '등록에 실패 하였습니다.',
                          icon: 'error',
                        });
                        console.error('Error:', error);
                      });
                  }
                });
            };



    const AutoLink = async () => {


        Swal.fire({
            title: '자동으로 화장품 구매처 정보를 가져오시겠습니까',
            icon: 'info',
            showCancelButton: true,
            confirmButtonText: '확인',
            cancelButtonText: '취소',
            }).then((res) => {
                if (res.isConfirmed) {

                Swal.fire({
                    title: '잠시만 기다려주세요...',
                    allowOutsideClick: false,
                    showConfirmButton: false,
                    willOpen: () => {
                        Swal.showLoading();
                    },
                    customClass: {
                        popup: 'my-custom-popup-class',
                    }
                    });
                    console.log("이름"+name);
                    axios.get(`http://ceprj.gachon.ac.kr:60006/api/admin/cosmetic/crawling/${name}`)
                    .then(response => {
                        Swal.close(); // 로딩 화면 닫기
                        console.log("제이슨 : " + response);
                        console.log("제이슨 : " + response.data);
                        return response.data;
                        
                    })
                    .then(data => {
                        console.log("데이타 : " + data)
                        if (data.hasOwnProperty('error')) {
                        Swal.fire({
                            title: '추출에 실패 하였습니다.',
                            icon: 'error',
                        });
                        } else {
                        Swal.fire({
                            title: '추출 되었습니다',
                            icon: 'success',
                        });
                        }
                        setSalesInfos(data.listCrawling);
                        console.log(data.listCrawling);
                    })
                    .catch(error => {
                        Swal.close(); // 로딩 화면 닫기
                        Swal.fire({
                        title: '추출에 실패 하였습니다.',
                        icon: 'error',
                        });
                        console.error('Error:', error);
                    });
                    }
                });
            };


            


    const goBack = () => {
        handleIsEditingChange();
      }

      console.log(Ingredient);
    const data = [{ name: "수동" }]
    const data2 = [{ name: "직접 입력" }, { name: "이미지 선택" }]
    return (
    <div className='TotalBox'> 
        <div className='registerBox'>
            <div className='selectImg'>
                <button onClick={handleFileInputClick} className="custom-file-upload">
                    {selectedFile ? (
                        <img src={selectedFilepre} alt="Selected" className='registerImg' />
                    ) : (
                        <div className='registerImg'><h2 className='imgtext'>이미지 선택</h2></div>
                    )}
                </button>
                <input id="file-upload1" type="file" accept="image/*" onChange={handleFileChange} ref={fileInputRef} style={{ display: 'none' }} />
            </div>
            <div className="registerbox">
                <div style={{ marginBottom: "36px" }}>
                    <span className='registerText'>화장품 이름 : </span>
                    <input
                        type="text"
                        value={name}
                        onChange={handleChange}
                        className="text-field2"
                    />
                </div>
                <div className='radio-container'>
                    <span className='registerText' style={{ marginRight: "32px" }} >성분</span>
                    <RegisterRow data={data2} selectedOption={selectedOption2} handleOptionChange={handleOptionChange2} />
                    {(selectedIFile != null) ? <button onClick={fetchImageServer} className='completebt1'>입력 완료</button> : <></>}
                    {(selectedIFile != null) ? <button onClick={handleImageReset} className='completebt2'>입력 취소</button> : <></>}
                </div>
                <div>
                    {selectedOption2 === '직접 입력' ? (
                        <textarea
                            value={Ingredient}
                            onChange={handleIngredientChange}
                            placeholder={`성분을 입력해주세요.\n(※직접 입력시 ',' 를 구분으로 적어주시고 성분만 적혀 있는 이미지가 있다면 이미지로 성분 등록이 가능합니다.)`}
                            className="registerIngredientField"
                            rows={10}
                            cols={50}
                        ></textarea>
                    ) : selectedOption2 === '이미지 선택' ? (
                        <div className='selectImg2'>
                            <button onClick={handleFileInputClick2} className="custom-file-upload">
                                {selectedIFile ? (
                                    <img src={selectedIFilepre} alt="Selected" className='registerImg2' />
                                ) : (
                                    <div className='registerImg2'><h2 className='imgtext'>성분<br />이미지 선택</h2></div>
                                )}
                            </button>
                            <input id="file-upload2" type="file" accept="image/*" onChange={handleIFileChange} ref={fileInputRef2} style={{ display: 'none' }} />
                        </div>
                    ) : (
                        <div className='registerImg'><h2 className='imgtext'></h2></div>
                    )}
                    
                </div>
                <span style={{ fontSize: "16px" }}></span>
                
                
            </div>
            <div className='linkBox'>
                    <div className='linkBoxIn' >
                        <div className='radio-container2'>
                            <span className='registerText' style={{ marginRight: "32px" }}>구매처 링크 및 가격</span>
                            <div className='link_auto_bts_box'>
                            <RegisterRow data={data} selectedOption={selectedOption} handleOptionChange={handleOptionChange} />
                            <button className='auto_bts' onClick={AutoLink}>자동</button>
                            </div>
                        </div>

                        {selectedOption == '수동' ? (<div className='linkBts'>
                            {salesInfos.map((salesInfo, index) => (
                                <SaleInfoInput
                                    key={index}
                                    salesInfo={salesInfo}
                                    onChange={(updatedInfo) => handlePurchaseChange(index, updatedInfo)}
                                />
                            ))}
                            <div className='linkBtsBox'>
                            <button className='dataAddBts' onClick={handleAddSaleInfo}>추가</button>
                            <button className='dataAddBts' onClick={handleRemoveSaleInfo}>제거</button>
                            </div>
                        </div>) : (<></>)}
                    </div>
                    {/* {(salesInfos[0].purchase_url != '' && salesInfos[0].purchase_name != '' && salesInfos[0].purchase_price != '') ? (<span className='completebt'>입력 완료</span>) : (<></>)} */}
                </div>
            <div className='bts'>
                    <button onClick={handleRegister} className='bt' >등록</button>
                    <button className='bt' onClick={goBack}>취소</button>
            </div>
        </div>
    </div>   
    );
}

export default Register;
