import React from 'react';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Filler,
  Legend,
} from 'chart.js';
import { Line } from 'react-chartjs-2';

  ChartJS.register(
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
  Tooltip,
  Filler,
  Legend,
  )

const AreaChart = ({ data }) => {
  const areaData = {
    labels: data.map((item) => item.date),
    datasets: [
      {
        fill: true,
        data: data.map((item) => item.activity),
        borderColor: 'rgb(53, 162, 235)',
        backgroundColor: 'rgba(53, 162, 235, 0.5)',
      },
    ],
  };

  const options = {
    responsive: true,
    plugins: {
      legend: {
        display:false
      },
      title: {
        display: true,
      },
    },
  };

  return (
    <div style={{ width: '450px', height: '300px' }}>
      <Line data={areaData} options={options} />
    </div>
  );
};

export default AreaChart;