import React, {useState} from "react"
import '../../css/AI_manage.css'

const HyperParameter = ({onClick}) => {

    return <div style={{width:'90%'}}>
    <div className="AIBox">
    <h2 style={{fontWeight:'bold'}} className="AIText">하이퍼파라미터</h2>
    <div>
    <div className="ParameterContainer">
        <div className='AIInputBox'>
        <span className='AIText2'>학습률 : </span>
        <input
            type="text"
            value='0000'
            className="AITextField"
        />
    </div>
    <div className='AIInputBox'>
        <span className='AIText2'>배치 크기 : </span>
        <input
            type="text"
            value='0000'
            className="AITextField"
        />
    </div>
    <div className='AIInputBox'>
        <span className='AIText2'>에포크 : </span>
        <input
            type="text"
            value='0000'
            className="AITextField"
        />
    </div>
    <div className="HyAIbts">
            <button className="AIbt" >변경</button>
            <button onClick={onClick} className="AIbt" >취소</button>
        </div>
    </div>
    </div>
</div>
</div>
}

export default HyperParameter