import React, {useState, useEffect} from 'react';
import '../../css/register_cosmetic.css'
import CosmeticTable from '../../component/register_cosmetic/cosmetic_table';

const columns = [
    { Header: '모두선택', accessor: '모두선택' },
    { Header: '', accessor: 'image_path' },
    { Header: '날짜', accessor: 'date' },
    { Header: '회원 id', accessor: 'login_id' },
    { Header: '화장품 이름', accessor: 'cosmetic_name' },
    { Header: '평가 점수', accessor: 'score' },
];

const RegisterCosmetic = () => {
  const [status, setStatus] = useState(0);
    const [data, setData] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
        try {
          const response = await fetch('http://ceprj.gachon.ac.kr:60006/api/admin/cosmetics/add/info');
          const fetchedData = await response.json();
          const modifiedData = fetchedData.map(item => ({ ...item, 모두선택: false }));
          setData(modifiedData);
        } catch (error) {
          console.error('Error fetching data:', error);
        }
      };
      setStatus(0);

    fetchData();
  }, [status]);

    return <div className='titleBox'>
        <div className="title">화장품 등록</div>
        <div className="title2">Cosmetics Register for admin</div><hr className="n1"/><br /><br /><br />
        <CosmeticTable columns={columns} data={data} status={status} setStatus={setStatus}  />
    </div>
}

export default RegisterCosmetic
